import { reassignContainer } from './container';
import { Node } from './node';
import {
  ContainerRegistryMetadata,
  GlobalNodeRegistryMetadata,
  IContainer,
  IFolderNode,
  INode,
  isFolderNode,
  isNode,
  SerializedFolderNode,
  SerializedNode,
} from './node-interface';
import { NodeTypeRegistry } from './node-type-registry';

export class NodeFactory {

  static create(serializedNode: SerializedNode, isNew: boolean = true, containerRegistry?: ContainerRegistryMetadata,
                globalRegistry?: GlobalNodeRegistryMetadata): Node {
    if (isFolderNode(serializedNode)) {
      const { ContainerNode } = require('./container-node');
      return ContainerNode.create(<SerializedFolderNode>serializedNode, isNew, containerRegistry, globalRegistry);
    }
    const node = new Node().deserialize(serializedNode, isNew);
    if (isFolderNode(node)) {
      return <Node><unknown>reassignContainer(<IFolderNode><unknown>node, (<IFolderNode>serializedNode).sub_container,
                                              containerRegistry, globalRegistry);
    }
    return node;
  }

  static createTempNode(type: string, position: [number, number]): Node {
    const node_class = NodeTypeRegistry.instance().get(type);
    if (node_class) {
      const node: Node = new node_class();
      node.type = type;
      node.pos = position;
      node.size = node.computeSize(true);
      return node;
    } else {
      throw `Can't create node of type [${type}]. Make sure it is registered through registerNodeType`;
    }
  }

  static toSimpleNode<T extends SerializedNode>(cid: number | string, id: number | string): T {
    return <T>{ id: id, cid: cid, type: '' };
  }

  static standardize(parent: IContainer, nodes: (number | string | SerializedNode | INode)[]): INode[] {
    return nodes.map(n => {
      const nn: INode = isNode(n) ? <INode>n : NodeFactory.toSimpleNode(parent.id, <string>n);
      nn.container = nn.container ?? parent;
      return nn;
    });
  }

}
