import { isNotNull, isNull } from '../../../utils/helper';
import { Node } from '../../node';
import { createNodeDbHelper } from '../../node-db-helper';
import { NODE_INFO } from '../../node-info';
import { NodeIO } from '../../node-io';

export abstract class AbstractFolderIONode extends Node {

  protected constructor(title: string, description: string) {
    super();
    this.title = title;
    this.description = description;
    this.iconImageUrl = NODE_INFO.container["folder-input"].iconImageUrl;
  }

  onAdd() {
    // To backward compatible
    if (!this.isUnderContainer() || isNull(this.properties['slot']) || this.properties['slot'] === -1) return;
    let slot = this.properties['slot'];
    delete this.properties['slot'];
    createNodeDbHelper().update(this, ['properties']);
    this.correctParentIO(this.container.nodeItself(), slot);
  }

  abstract onRemoved();

  abstract onAfterSettingsChange();

  protected abstract correctParentIO(containerNode: Node, slot: number);

  protected isUnderContainer() {
    return isNotNull(this.container?.nodeItself());
  }

  protected findParentIOById(inputOrOutput: string) {
    return Object.entries(this.container.nodeItself()[inputOrOutput])
                 .find(([_, io]) => this.id === (<NodeIO>io).subNodeId) ?? [null, null];
  }

  protected hasItself(inputOrOutput: string): boolean {
    return this.findParentIOById(inputOrOutput)[0] !== null;
  }
}
