import { IOType } from '../nodes/node-io';
import { compareNodeIO, SlimNodeIO } from '../nodes/node-link';
import { ApiResult } from './api-result';

export class LinkResult implements ApiResult<object> {

  message: string;
  private readonly _map: {
    [cid: string]: {
      [nid: string]: {
        nid: string,
        inputs: { [ioId: string]: SlimNodeIO },
        outputs: { [ioId: string]: SlimNodeIO[] }
      }
    }
  };

  constructor(data?: {
    [cid: string]: {
      [nid: string]: {
        nid: string,
        inputs: { [ioId: string]: SlimNodeIO },
        outputs: { [ioId: string]: SlimNodeIO[] }
      }
    }
  }) {
    this._map = data ?? {};
  }

  push(cid: string | number, nid: string | number, type: IOType, ioId: string | number,
       ...links: SlimNodeIO[]): LinkResult {
    this._map[cid] = this._map[cid] ?? {};
    this._map[cid][nid] = this._map[cid][nid] ?? { nid: nid + '', inputs: {}, outputs: {} };
    if (type === 'input') {
      this._map[cid][nid].inputs[ioId] = (links ?? [])[0] ?? null;
    }
    if (type === 'output') {
      const o = this._map[cid][nid].outputs[ioId] ?? [];
      const u = links ?? [];
      this._map[cid][nid].outputs[ioId] = o.concat(u.filter(io1 => !o.some(io2 => compareNodeIO(io1, io2))));
    }
    return this;
  }

  hasInput(cid: string | number, nid: string | number, ioId: string | number): boolean {
    return this.getLinkByInput(cid, nid, ioId) !== undefined;
  }

  getLinkByInput(cid: string | number, nid: string | number, ioId: string | number): SlimNodeIO {
    return (this._map[cid] ?? {})[nid]?.inputs[ioId];
  }

  getLinkByOutput(cid: string | number, nid: string | number, ioId: string | number): SlimNodeIO[] {
    return (this._map[cid] ?? {})[nid]?.outputs[ioId];
  }

  result(): {
    [cid: string]: {
      [nid: string]: {
        nid: string,
        inputs: { [ioId: string]: SlimNodeIO },
        outputs: { [ioId: string]: SlimNodeIO[] }
      }
    }
  } {
    return this._map;
  }

  merge(r: LinkResult): LinkResult {
    Object.keys(r.result()).forEach(cid => Object.values(r.result()[cid]).forEach(d => {
      Object.keys(d.inputs).forEach(k => this.push(cid, d.nid, 'input', k, d.inputs[k]));
      Object.keys(d.outputs).forEach(k => this.push(cid, d.nid, 'output', k, ...d.outputs[k]));
    }));
    return this;
  }

}
