import { Enumify } from 'enumify';
import { isFunction, isNotNull } from '../utils/helper';
import { BaseIdGenerator } from '../utils/id-generator';
import { NodeIO, SerializedNodeInput, SerializedNodeOutput, SerializedNodeSetting } from './node-io';
import { Registry } from './registry';
import { Serializable } from './serializable';

export class Side extends Enumify {

  static server = new Side('SERVER');
  static editor = new Side('EDITOR');
  static dashboard = new Side('DASHBOARD');
  static _ = Side.closeEnum();

  private readonly name: string;

  private constructor(name: string) {
    super();
    this.name = name;
  }

  toString(): string {
    return this.name;
  }
}

/**
 * Represents node in serialized mode to persist in database or json data in import/export
 */
export interface SerializedNode {
  id: string | number;
  cid: string | number;
  name: string;
  type: string;
  pos: [number, number];
  size?: [number, number];
  inputs?: { [id: number]: SerializedNodeInput };
  outputs?: { [id: number]: SerializedNodeOutput };
  properties?: any;
  settings?: { [name: string]: SerializedNodeSetting };
  flags?: any;
  icon?: string;

}

export interface INode extends SerializedNode, Serializable<SerializedNode> {
  /**
   * Parent container
   */
  //TODO: rename to `parent`
  container: IContainer;
  side: Side;
  title: string;
  category: string;
  inputs?: { [id: number]: SerializedNodeInput & NodeIO };
  outputs?: { [id: number]: SerializedNodeOutput & NodeIO };

  isUpdated?: boolean;

  /**
   * Gets parent container
   * @see INode#container
   * @return {IContainer} parent container
   */
  getParent(): IContainer;

  /**
   * Gets node that represents to {@link INode#container}
   * @see IContainer#nodeItself
   * @return {IFolderNode} `null` if parent container is root container, otherwise folder node that represents itself
   */
  getParentNode(): IFolderNode;
}

/**
 * Represents folder node in serialized mode to persist in database or json data in import/export
 */
export interface SerializedFolderNode extends SerializedNode {

  /**
   * Represents itself container that contains other nodes
   */
  sub_container: SerializedContainer;

}

export interface IFolderNode extends SerializedFolderNode, INode, Serializable<SerializedFolderNode> {

  /**
   * @see SerializedFolderNode#sub_container
   */
  sub_container: IContainer;
  inputs?: { [id: number]: SerializedNodeInput & NodeIO };
  outputs?: { [id: number]: SerializedNodeOutput & NodeIO };

  serialize(): SerializedFolderNode;
}

/**
 * Represents container in serialized mode to persist in database or json data in import/export
 */
export interface SerializedContainer {
  id: string | number;
  name?: string;
  config?: any;
  serializedNodes?: SerializedNode[];
}

export interface IContainer extends SerializedContainer, Serializable<SerializedContainer> {

  side: Side;

  /**
   * Gets a node that represents itself.
   * @return {IFolderNode} `null` if this container is root container, otherwise folder node that represents itself
   */
  nodeItself(): IFolderNode;

  nodeRegistry(): SubNodeRegistryMetadata;
}

export const isNode = (node: any): boolean => {
  return isNotNull(node) && typeof node === 'object' && 'id' in node && 'cid' in node && 'type' in node;
};

export const isFolderNode = (node: any): boolean => {
  return isNode(node) && 'sub_container' in node;
};

export const isSerializedContainer = (container: any): boolean => {
  return isNotNull(container) && !isNode(container) && typeof container === 'object' && 'id' in container;
};

export const isContainer = (container: any): boolean => {
  return isSerializedContainer(container) && isFunction(container['nodeRegistry']);
};

export interface GeneratedKeyRegistry<T> extends Registry<T> {

  readonly idGenerator: BaseIdGenerator;

}

export interface ContainerRegistryMetadata extends GeneratedKeyRegistry<IContainer> {

  /**
   * Register new container
   * @param {IContainer} container
   * @param force if `false` and container id is already existed, new container id will be generated then reassigned
   * to given container
   * @return {IContainer} the registered container, never null
   * @throws RegistryError if given container is null
   */
  register(container: IContainer, force?: boolean): IContainer;

  /**
   * Get container
   * @param {number | string} id
   * @returns {SerializedContainer} `null|undefined` if not found
   */
  get(id: number | string): IContainer;

  has(id: number | string): boolean;

  remove(id: number | string): IContainer;

  rootContainer(): IContainer;
}

export interface GlobalNodeRegistryMetadata extends Registry<INode> {

  register(container: INode, force?: boolean): INode;

  remove(id: number | string | INode): INode;

}

/**
 * Represents sub node registry in each container
 */
export interface SubNodeRegistryMetadata extends GeneratedKeyRegistry<INode> {

  globalNodeRegistry: GlobalNodeRegistryMetadata;
  containerRegistry: ContainerRegistryMetadata;

  parent(): IContainer;

  /**
   * Returns a sub node by given id
   * @param node
   * @return {INode} sub node or null if not found
   */
  get(node: number | string | INode): INode;

  /**
   *
   * @param {INode} node
   * @param {boolean} force? Force register
   * @param {string} key? node key
   * @param {boolean} isCaching? only caching without calling to backend or database
   * @return {Promise<INode>}
   */
  register(node: INode, force?: boolean, key?: string, isCaching?: boolean): Promise<INode>;

  /**
   * Removes a node from the container
   * @param node the instance of the node or node id
   * @param {boolean} isCaching? only caching without calling to backend or database
   */
  remove(node: INode | number | string, isCaching?: boolean): Promise<INode>;

}

