import { Request, Response } from 'express';
import { SerializedContainer, SerializedNode } from '../../../nodes/node-interface';
import { NodeValidator } from '../../../shared/validator';
import {
  CLONE_NODES,
  CREATE_NODE,
  EXPORT_NODES,
  IMPORT_NODES,
  PACK_UP_NODES,
  REMOVE_NODES,
} from '../../../shared/websocket-events';
import { RegistryItemExistedError } from '../../../utils/errors';
import { reliability_logger } from '../../../utils/loggers';
import { DashboardServerSocket } from '../../dashboard-server-socket';
import { EditorServerSocket } from '../../editor-server-socket';
import { NodeCRUDService } from '../../service/NodeCRUDService';
import { NodeETLService } from '../../service/NodeETLService';
import { ApiHandler } from './api-handler';

export class ApiNodeHandler extends ApiHandler {

  private readonly dashboardSocket: DashboardServerSocket;
  private readonly crudService = new NodeCRUDService();
  private readonly etlService = new NodeETLService();

  constructor(editorSocket: EditorServerSocket, dashboardSocket: DashboardServerSocket) {
    super(editorSocket);
    this.dashboardSocket = dashboardSocket;
  }

  onList(req: Request, resp: Response): void {
    this.crudService.getNodes(req.params.cid)
        .then(containers => resp.status(200).json(containers))
        .catch(err => {
          reliability_logger.warn(err);
          resp.status(500).json(<SerializedContainer>{ id: req.params.cid, serializedNodes: [], err: err.msg });
        });
  }

  onCreate(req: Request, resp: Response): void {
    this.crudService.createNode(req.params.cid, <SerializedNode>{ type: req.body.type, pos: req.body.position })
        .then(result => {
          if (result.isDashboardNode) {
            this.dashboardSocket.io.in(req.params.cid).emit(CREATE_NODE, result.result());
          }
          return this.emitSuccess(resp, CREATE_NODE, result, 201);
        })
        .catch(err => this.emitError(resp, err, CREATE_NODE,
                                     e => e instanceof RegistryItemExistedError ? 409 : 400));
  }

  onRemove(req: Request, resp: Response): void {
    this.crudService.removeNodes(req.params.cid, req.body, true)
        .then(result => this.emitSuccess(resp, REMOVE_NODES, result))
        .catch(err => this.emitError(resp, err, REMOVE_NODES));
  }

  onPackUp(req: Request, resp: Response): void {
    const cId = req.params.cid;
    const { ids, pos } = req.body;
    this.etlService.packUpNodes(cId, ids, pos)
        .then(result => {
          this.editorSocket.io.emit(REMOVE_NODES, result.cleanupResult.result());
          return this.emitSuccess(resp, PACK_UP_NODES, result);
        })
        .catch(err => this.emitError(resp, err, PACK_UP_NODES));
  }

  onClone(req: Request, resp: Response): void {
    const { ids, pos } = req.body;
    this.etlService.cloneNodes(req.params.cid, ids, pos)
        .then(result => this.emitSuccess(resp, CLONE_NODES, result))
        .catch(err => this.emitError(resp, err, CLONE_NODES));
  }

  onExport(req: Request, resp: Response): void {
    this.etlService.exportNodes(req.params.cid, req.body)
        .then(output => resp.status(200).json(output))
        .catch(err => this.emitError(resp, err, EXPORT_NODES));
  }

  onImport(req: Request, resp: Response) {
    const cId = req.params.cid;
    const { nodes, pos } = req.body;
    const output = NodeValidator.NodeArrayValidator().validate(nodes, { allowUnknown: true });
    if (output.error) {
      return this.emitError(resp, new Error(output.error), IMPORT_NODES);
    }
    this.etlService.importNodes(cId, nodes as SerializedNode[], pos)
        .then(result => this.emitSuccess(resp, IMPORT_NODES, result, 201))
        .catch(err => this.emitError(resp, err, IMPORT_NODES));
  }

}
