import { Colors, create } from 'logplease';
import { LinkResult } from '../shared/link-result';
import {
  ContainerRegistryValidator,
  IContainerRegistryValidator,
  INodeRegistryValidator,
  NodeRegistryValidator,
} from '../shared/validator';
import { NotFoundError } from '../utils/errors';
import { groupBy, isEmpty, isNull } from '../utils/helper';
import { IdGenerator } from '../utils/id-generator';
import { Node } from './node';
import { createNodeDbHelper, NodeDbHelper } from './node-db-helper';
import { ContainerRegistryMetadata, IContainer, INode } from './node-interface';
import { IOType } from './node-io';
import { Deserializable, Serializable } from './serializable';

const logger = create('node-link', { color: Colors.Magenta });

type LinkAction = 'Connecting' | 'Connected' | 'Disconnecting' | 'Disconnected' | 'Moving' | 'Moved';

/**
 * Represents simplest node io information
 */
export interface SlimNodeIO {
  nodeId: string | number;
  slot: number;
  ioId?: string | number;
}

export interface NodeLink {
  /**
   * Current container id
   */
  readonly cid: string;
  /**
   * Represents to a specific node output.
   * @see SlimNodeIO
   */
  readonly source: SlimNodeIO;
  /**
   * Represents to one or many node inputs
   * @see SlimNodeIO
   */
  targets: SlimNodeIO[];

}

/**
 * Represents for the bidirectional node link that guards for per input is connecting to only one another node output
 */
export interface BiNodeLink {
  /**
   * Current container id
   */
  readonly cid: string;

  add(...nodeLinks: NodeLink[]): BiNodeLink;

  optimize(): NodeLink[];
}

export const createNodeLink = (cid: string | number, source: SlimNodeIO, ...targets: SlimNodeIO[]): NodeLink => {
  return { cid: cid + '', source, targets };
};

export const createBiNodeLink = (cid: string | number): BiNodeLink => {
  return new DefaultBiNodeLink(cid + '');
};

export const nodeIOtoMsg = (type: IOType, cid: string | number, ...nodeIO: SlimNodeIO[]): string => {
  const msg = nodeIO.map(io => `node '${IdGenerator.createNodeId(cid, io.nodeId)}'-${type} '${io.ioId}'`).join(',');
  return isEmpty(msg) ? msg : `[${msg}]`;
};

export const slimIOtoKey = (io: SlimNodeIO): string => {
  return io.nodeId + '/' + (io.ioId ?? io.slot);
};

export const compareNodeIO = (io1: SlimNodeIO, io2: SlimNodeIO): boolean => {
  if (isNull(io1) || isNull(io2)) {
    return false;
  }
  return slimIOtoKey(io1) === slimIOtoKey(io2);
};

class DefaultBiNodeLink implements BiNodeLink {
  readonly cid: string;
  private readonly _bySource: { [sKey: string]: { source: SlimNodeIO, targets: { [tKey: string]: SlimNodeIO } } };
  private readonly _byTarget: { [tKey: string]: string };

  constructor(cid: string) {
    this.cid = cid;
    this._bySource = {};
    this._byTarget = {};
  }

  add(...nodeLinks: NodeLink[]): BiNodeLink {
    nodeLinks.filter(nl => nl.cid + '' === this.cid)
             .forEach(nl => {
               const sKey = slimIOtoKey(nl.source);
               this._bySource[sKey] = this._bySource[sKey] ?? { source: nl.source, targets: {} };
               nl.targets.forEach(io => {
                 const tKey = slimIOtoKey(io);
                 if (isNull(this._byTarget[tKey])) {
                   this._bySource[sKey].targets[tKey] = io;
                   this._byTarget[tKey] = sKey;
                 }
               });
             });
    return this;
  }

  optimize(): NodeLink[] {
    return Object.values(this._bySource)
                 .map(v => createNodeLink(this.cid, v.source, ...Object.values(v.targets)));
  }

}

/**
 * It is for legacy code. Should migrate later
 */
export interface LegacyLink {
  target_node_id: string | number;
  target_slot: number;
  target_input_id?: string | number;
}

export class Link implements SlimNodeIO, Serializable<SlimNodeIO>, Deserializable<LegacyLink | SlimNodeIO> {

  nodeId: string;
  slot: number;
  ioId?: string;

  constructor();
  constructor(nodeId: string | number, slot: number);
  constructor(nodeId: string | number, slot: number, ioId: string | number);
  constructor(nodeId?: string | number, slot?: number, ioId?: string | number) {
    this.nodeId = nodeId + '';
    this.slot = slot;
    this.ioId = (ioId ?? slot) + '';
  }

  static create(object: SlimNodeIO | Link | LegacyLink | { target_node_id: string | number, target_slot: number, target_input_id?: number }): Link {
    if (isNull(object)) {
      return null;
    }
    return new Link().deserialize(object);
  }

  serialize(): SlimNodeIO {
    return { ...this };
  }

  deserialize(obj: LegacyLink | SlimNodeIO): Link {
    return new Link((<SlimNodeIO>obj).nodeId ?? (<LegacyLink>obj).target_node_id,
                    (<SlimNodeIO>obj).slot ?? (<LegacyLink>obj).target_slot,
                    (<SlimNodeIO>obj).ioId ?? (<LegacyLink>obj).target_input_id);
  }
}

export interface NodeLinkHandler {

  /**
   * Create new link between source node to one or many nodes
   * @param {NodeLink} nodeLink
   * @return {LinkResult}
   */
  create(nodeLink: NodeLink): LinkResult;

  /**
   * Create new link between source node to one or many nodes
   * @param {NodeLink} nodeLink
   * @param {boolean} lenient if true, if node link targets has any invalid, it will skip to create link. Otherwise,
   * error will be thrown
   * @return {LinkResult}
   */
  create(nodeLink: NodeLink, lenient?: boolean): LinkResult;

  /**
   * Batch create multiple links between source node to one or many nodes.
   *
   * This method will optimize node-link to ensure each input node will have only one link to another one.
   *
   * @param {NodeLink} nodeLinks given node links
   * @return {LinkResult}
   */
  batchCreate(...nodeLinks: NodeLink[]): LinkResult;

  remove(nodeLink: NodeLink): LinkResult;

  remove(nodeLink: NodeLink, lenient?: boolean): LinkResult;

  move(type: IOType, fromNodeLink: NodeLink, toAnother: SlimNodeIO): LinkResult;

  disconnectInput(cid: string | number, slimIO: SlimNodeIO): LinkResult;

  disconnectOutput(cid: string | number, slimIO: SlimNodeIO): LinkResult;

}

/**
 * @deprecated Use {@link NodeLinkHandler}
 */
export interface LegacyLinkHandler {
  readonly node: Node;

  /**
   * Recompute Input links
   * @param {INode} node
   */
  recomputeInputLinks(node?: INode);

  /**
   * Recompute Output links
   * @param {INode} node?
   */
  recomputeOutputLinks(node?: INode);

}

/**
 * @deprecated Use {@link createLinkHandler}
 */
export const createLegacyLinkHandler = (node: Node, dbHelper?: NodeDbHelper): LegacyLinkHandler => {
  return new DefaultLinkHandler(node, dbHelper ?? createNodeDbHelper());
};

class DefaultLinkHandler implements LegacyLinkHandler {
  readonly node: Node;
  private readonly dbHelper: NodeDbHelper;

  constructor(node: Node, dbHelper: NodeDbHelper) {
    this.node = node;
    this.dbHelper = dbHelper;
  }

  recomputeInputLinks(n?: INode) {
    let node = n ?? this.node;
    let slot = -1;
    for (const [inputId, input] of Object.entries(node.inputs)) {
      if (input.setting.hidden) {
        continue;
      }
      slot++;
      if (isNull(input.link)) {
        continue;
      }
      let link = input.link;
      let targetNode = node.container.nodeRegistry().get(link.nodeId);
      if (isNull(targetNode)) {
        continue;
      }
      let outputId = link.ioId;
      let outputSlot = link.slot;
      let output = targetNode.outputs[outputId ?? outputSlot];
      if (isNull(output) || isNull(output.links)) {
        continue;
      }
      let number = output.links.findIndex(l => compareNodeIO(l, { nodeId: node.id, ioId: +inputId, slot }));
      if (number === -1) {
        continue;
      }
      output.links.splice(number, 1, new Link(node.id, slot, inputId));
      this.dbHelper.update(<Node>targetNode, ['outputs']);
    }
    this.dbHelper.update(<Node>node, ['inputs']);
  }

  recomputeOutputLinks(n?: INode) {
    let node = n ?? this.node;
    let slot = -1;
    Object.entries(node.outputs).forEach(([oId, output]) => {
      slot++;
      if (isEmpty(output.links)) {
        return;
      }
      output.links.forEach(link => {
        let target = node.container.nodeRegistry().get(link.nodeId);
        const iId = link.ioId ?? link.slot;
        if (isNull(target) || isNull(target.inputs[iId])) {
          return;
        }
        target.inputs[iId].link = new Link(node.id, slot, oId);
        this.dbHelper.update(<Node>target, ['inputs']);
      });
    });
    this.dbHelper.update(<Node>node, ['outputs']);
  }

}

export const createLinkHandler = (registry: ContainerRegistryMetadata, validator?: IContainerRegistryValidator,
                                  nodeValidator?: INodeRegistryValidator, lenient?: boolean): NodeLinkHandler => {
  return new DefaultNodeLinkHandler(registry, validator, nodeValidator, lenient);
};

class DefaultNodeLinkHandler implements NodeLinkHandler {

  protected readonly validator: IContainerRegistryValidator;
  protected readonly nodeValidator: INodeRegistryValidator;
  protected readonly globalStrict: boolean;

  constructor(registry: ContainerRegistryMetadata, validator?: IContainerRegistryValidator,
              nodeValidator?: INodeRegistryValidator, lenient: boolean = false) {
    this.validator = validator ?? new ContainerRegistryValidator(registry);
    this.nodeValidator = nodeValidator ?? new NodeRegistryValidator();
    this.globalStrict = !lenient;
  }

  private static msg(cId: number | string, bT: IOType, base: SlimNodeIO, rT?: IOType,
                     ...references: SlimNodeIO[]): [string, string] {
    return [nodeIOtoMsg(bT, cId, base), nodeIOtoMsg(rT, cId, ...references)];
  }

  private static statement(action: LinkAction, len: number | 'all', msg: [string, string],
                           direction: 'from' | 'to' = 'from') {
    const first = direction === 'from' ? `from ${msg[0]}` : `to ${msg[0]}`;
    const next = isEmpty(msg[1]) ? '' : `${direction === 'from' ? 'to' : 'from'} ${msg[1]}`;
    const last = action.includes('ing') ? '...' : '';
    return `${action} ${len} link(s) ${first} ${next}${last}`;
  }

  create(nodeLink: NodeLink, lenient?: boolean): LinkResult {
    const optimized = createBiNodeLink(nodeLink.cid).add(nodeLink).optimize()[0];
    const msg = DefaultNodeLinkHandler.msg(nodeLink.cid, 'output', optimized.source, 'input', ...optimized.targets);
    logger.info(DefaultNodeLinkHandler.statement('Connecting', optimized.targets.length, msg));
    const r = this.doCreate(optimized, lenient);
    r.message = DefaultNodeLinkHandler.statement('Connected', optimized.targets.length, msg);
    return r;
  }

  batchCreate(...nodeLinks: NodeLink[]): LinkResult {
    return Object.entries(groupBy(nodeLinks, nl => nl.cid, nl => [nl]))
                 .map(([k, v]) => createBiNodeLink(k).add(...v))
                 .flatMap(bi => bi.optimize())
                 .reduce((r, nl) => r.merge(this.doCreate(nl, true)), new LinkResult());
  }

  remove(nodeLink: NodeLink, lenient?: boolean): LinkResult {
    const container = this.validator.validate(nodeLink.cid);
    const optimized = createBiNodeLink(nodeLink.cid).add(nodeLink).optimize()[0];
    const msg = DefaultNodeLinkHandler.msg(optimized.cid, 'output', optimized.source, 'input', ...optimized.targets);
    logger.info(DefaultNodeLinkHandler.statement('Disconnecting', optimized.targets.length, msg));
    const r = this.doDisconnectOutput(container, optimized.source, !lenient,
                                      io => optimized.targets.some(v => compareNodeIO(v, io)));
    r.message = DefaultNodeLinkHandler.statement('Disconnected', optimized.targets.length, msg);
    return r;
  }

  move(type: IOType, fromNode: NodeLink, toAnother: SlimNodeIO): LinkResult {
    let newOne: NodeLink, base: SlimNodeIO;
    const optimized = createBiNodeLink(fromNode.cid).add(fromNode).optimize()[0];
    if (type === 'input') {
      if (optimized.targets.length != 1) {
        throw new Error(`Cannot move empty or more than one input's link to another node`);
      }
      newOne = createNodeLink(optimized.cid, optimized.source, toAnother);
      base = optimized.targets[0];
    } else {
      newOne = createNodeLink(optimized.cid, toAnother, ...optimized.targets);
      base = optimized.source;
    }
    const msg = DefaultNodeLinkHandler.msg(optimized.cid, type, base, type, toAnother);
    logger.info(DefaultNodeLinkHandler.statement('Moving', optimized.targets.length, msg));
    const r = this.remove(optimized, true).merge(this.doCreate(newOne, true, ...optimized.targets));
    r.message = DefaultNodeLinkHandler.statement('Moved', optimized.targets.length, msg);
    return r;
  }

  disconnectInput(cid: string | number, slimIO: SlimNodeIO): LinkResult {
    const msg = DefaultNodeLinkHandler.msg(cid, 'input', slimIO);
    logger.info(DefaultNodeLinkHandler.statement('Disconnecting', 'all', msg, 'to'));
    return this.doDisconnectInput(this.validator.validate(cid), slimIO, false);
  }

  disconnectOutput(cid: string | number, slimIO: SlimNodeIO): LinkResult {
    const msg = DefaultNodeLinkHandler.msg(cid, 'output', slimIO);
    logger.info(DefaultNodeLinkHandler.statement('Disconnecting', 'all', msg));
    return this.doDisconnectOutput(this.validator.validate(cid), slimIO, false);
  }

  protected doCreate(nodeLink: NodeLink, lenient?: boolean, ...skipSourceLinks: SlimNodeIO[]): LinkResult {
    const container = this.validator.validate(nodeLink.cid);
    const source = this.validateSource(container, nodeLink.source, this.globalStrict);
    if (isNull(source.io)) {
      return new LinkResult();
    }
    return nodeLink.targets
                   .map(target => this.doConnect(container, source, target, !lenient, skipSourceLinks))
                   .reduce((prev, current) => prev.merge(current), new LinkResult());
  }

  protected validateTarget(container: IContainer, current: SlimNodeIO,
                           strict: boolean): { io: SlimNodeIO, link: SlimNodeIO } {
    const n = this.nodeValidator.validate(current.nodeId, container, strict);
    const inputId = current.ioId ?? current.slot;
    const input = isNull(n) ? null : n.inputs[inputId];
    const isUnlinkable = isNull(input) || input.setting.hidden;
    if (strict && isUnlinkable) {
      throw new NotFoundError(`Not found input id ${inputId} in ` +
                              `node ${IdGenerator.createNodeId(container.id, current.nodeId)}`);
    }
    return { io: isUnlinkable ? null : current, link: isUnlinkable ? null : Link.create(input.link) };
  }

  protected validateSource(container: IContainer, current: SlimNodeIO,
                           strict: boolean): { io: SlimNodeIO, links: SlimNodeIO[] } {
    const n = this.nodeValidator.validate(current.nodeId, container, strict);
    const outputId = current.ioId ?? current.slot;
    const output = isNull(n) ? null : n.outputs[outputId];
    if (strict && isNull(output)) {
      throw new NotFoundError(`Not found output id ${outputId} in ` +
                              `node ${IdGenerator.createNodeId(container.id, current.nodeId)}`);
    }
    return { io: isNull(output) ? null : current, links: (output?.links ?? []).map(link => Link.create(link)) };
  }

  private doConnect(container: IContainer, source: { io: SlimNodeIO; links: SlimNodeIO[] }, target: SlimNodeIO,
                    strict: boolean, skipSourceLinks: SlimNodeIO[]): LinkResult {
    const msg = DefaultNodeLinkHandler.msg(container.id, 'output', source.io, 'input', target);
    logger.debug(DefaultNodeLinkHandler.statement('Connecting', 1, msg));
    const r = this.doDisconnectInput(container, target, strict, skipSourceLinks);
    if (r.hasInput(container.id, target.nodeId, target.ioId)) {
      logger.debug(DefaultNodeLinkHandler.statement('Connected', 1, msg));
      const skip = isEmpty(skipSourceLinks) ? [target] : [...skipSourceLinks, target];
      const links = source.links.filter(io1 => !skip.some(io2 => compareNodeIO(io1, io2)));
      return r.push(container.id, target.nodeId, 'input', target.ioId, source.io)
              .push(container.id, source.io.nodeId, 'output', source.io.ioId, ...[...links, target]);
    }
    return r;
  }

  private doDisconnectInput(container: IContainer, target: SlimNodeIO, strict: boolean,
                            skipSourceLinks?: SlimNodeIO[]): LinkResult {
    const targetIO = this.validateTarget(container, target, strict);
    if (isNull(targetIO.io)) {
      return new LinkResult();
    }
    const r = new LinkResult().push(container.id, target.nodeId, 'input', target.ioId);
    if (isNull(targetIO.link)) {
      return r;
    }
    const updated = this.validateSource(container, targetIO.link, false).links
                        .filter(link => !compareNodeIO(link, target));
    const links = isEmpty(skipSourceLinks) ? updated :
                  updated.filter(io1 => !skipSourceLinks.some(io2 => compareNodeIO(io1, io2)));
    logger.debug(DefaultNodeLinkHandler.statement('Disconnected', links.length,
                                                  DefaultNodeLinkHandler.msg(container.id, 'input', target), 'to'));
    return r.push(container.id, targetIO.link.nodeId, 'output', targetIO.link.ioId, ...links);
  }

  private doDisconnectOutput(container: IContainer, source: SlimNodeIO, strict: boolean,
                             filter: (io: SlimNodeIO) => boolean = _ => true): LinkResult {
    const sourceIO = this.validateSource(container, source, strict);
    const result = new LinkResult().push(container.id, source.nodeId, 'output', source.ioId);
    sourceIO.links
            .filter(io => compareNodeIO(this.validateTarget(container, io, false).link, source))
            .forEach(io => {
              if (filter(io)) {
                result.push(container.id, io.nodeId, 'input', io.ioId);
              } else {
                result.push(container.id, source.nodeId, 'output', source.ioId, io);
              }
            });
    logger.debug(DefaultNodeLinkHandler.statement('Disconnected', sourceIO.links.length,
                                                  DefaultNodeLinkHandler.msg(container.id, 'output', source)));
    return result;
  }
}
