import { Response } from 'express';
import { NotFoundError } from '../../../utils/errors';
import { isNotNull } from '../../../utils/helper';
import { reliability_logger } from '../../../utils/loggers';
import { EditorServerSocket } from '../../editor-server-socket';
import { ApiResult } from '../../../shared/api-result';

export abstract class ApiHandler {
  protected readonly editorSocket: EditorServerSocket;

  protected constructor(editorSocket: EditorServerSocket) {
    this.editorSocket = editorSocket;
  }

  protected emitSuccess(resp: Response, event: string, result: ApiResult<any>, code: number = 200) {
    this.editorSocket.io.emit(event, result.result());
    return resp.status(code).send(result.message);
  }

  protected emitError(resp: Response, err: Error, event: string, errorFunc?: (e: Error) => number) {
    reliability_logger.warn(err.stack ?? err.message);
    return resp.status(err instanceof NotFoundError ? 404 : isNotNull(errorFunc) ? errorFunc(err) : 400)
               .send(`Can't ${event.toLowerCase()}. Reason: ${err}`);
  }
}
