import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class JsonObjFilter extends Node {
  constructor() {
    super();
    this.title = NODE_INFO.json["json-obj-filter"].title;
    this.description = NODE_INFO.json["json-obj-filter"].description;
    this.addInput('input', Type.JSON);
    this.addOutput('output', Type.JSON);
    this.settings['key'] = NodeSetting.create({
      description: 'Example: key',
      value: 'key',
      type: SettingType.STRING,
    });
    this.settings['filter'] = NodeSetting.create({
      description: 'Example: value',
      value: 'value',
      type: SettingType.STRING,
    });
  }


  onInputUpdated() {
    const input = this.getInputData(0);
    if (input === null) {
      this.setOutputData(0, null);
      return;
    }

    try {
      const key = this.settings['key'].value;
      const filter = this.settings['filter'].value;
      const out = input.filter(obj => (obj[key] === filter)); 
      this.setOutputData(0, out);
    } catch (e) {
      this.setOutputData(0, null);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('json/json-obj-filter', JsonObjFilter);
