import { NodeDbHelper } from '../../nodes/node-db-helper';
import { ContainerRegistryMetadata } from '../../nodes/node-interface';
import { IOType } from '../../nodes/node-io';
import { createLinkHandler, createNodeLink, NodeLink, NodeLinkHandler, SlimNodeIO } from '../../nodes/node-link';
import { LinkResult } from '../../shared/link-result';
import { BackendLinkVisitor } from '../../shared/link-result-visitor';
import { NodeService } from './node-service';

export class NodeLinkService extends NodeService {
  private readonly handler: NodeLinkHandler;

  constructor(cRegistry?: ContainerRegistryMetadata, dbHelper?: NodeDbHelper) {
    super(cRegistry, dbHelper);
    this.handler = createLinkHandler(this.cRegistry);
  }

  create(cid: number | string, source: SlimNodeIO, ...targets: SlimNodeIO[]): Promise<LinkResult> {
    return Promise.resolve(createNodeLink(cid, source, ...targets))
                  .then(nodeLink => this.handler.create(nodeLink))
                  .then(r => this.persist(r));
  }

  remove(cid: number | string, source: SlimNodeIO, ...targets: SlimNodeIO[]): Promise<LinkResult> {
    return Promise.resolve(createNodeLink(cid, source, ...targets))
                  .then(nodeLink => this.handler.remove(nodeLink))
                  .then(r => this.persist(r));
  }

  move(type: IOType, fromLink: NodeLink, toAnother: SlimNodeIO): Promise<LinkResult> {
    return Promise.resolve(this.handler.move(type, fromLink, toAnother)).then(r => this.persist(r));
  }

  disconnect(): Promise<LinkResult> {
    return null;
  }

  private persist(r: LinkResult): Promise<LinkResult> {
    return new BackendLinkVisitor(this.cRegistry).visit(r)
                                                 .then(re => {
                                                   this.logger.info(re.message);
                                                   return re;
                                                 });
  }
}

