import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import {Type } from '../../node-io';
import { isNull } from '../../../utils/helper';


class JsonPreventDuplicates extends Node {
  older;
  constructor() {
    super();
    this.title = NODE_INFO.json["json-prevent-duplicates"].title;
    this.description = NODE_INFO.json["json-prevent-duplicates"].description;
    this.addInput('input', Type.JSON);
    this.addOutput('output', Type.JSON);
  }

  getChanges = function (oldArray: any, newArray: any) {
    if (JSON.stringify(oldArray) === JSON.stringify(newArray)) {
      return false;
    } else return true;
  
  };


  onInputUpdated() {
    const input = this.getInputData(0);
    if (input === null) {
      this.setOutputData(0, null);
      return;
    }
    try {
      // first run
      if(isNull(this.older)){
        this.setOutputData(0, input);
        this.older = input
      }
      const test = this.getChanges(this.older, input)
      if(test == false) return
      if(test == true){
        this.older = input
        this.setOutputData(0, input);
      }
    
    } catch (e) {
      this.setOutputData(0, null);
    }
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }
}
Container.registerNodeType('json/json-prevent-duplicates', JsonPreventDuplicates);
