import Utils from '../../../utils';
import { isNull } from '../../../../utils/helper';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { NodeSetting, SettingType, Type } from '../../../node-io';
import { Side } from '../../../node-interface';
import config from '../../../../config';
import axios from 'axios';
const matchMQTT = require('mqtt-match');
import * as mqtt from 'mqtt';

interface MQTTMessage {
  topic: string;
  message: string;
}


/*
- on created call api to get all points
- on added try connected to mqtt broker
- on save settings 
-- set MQTT topic if user selected a point
-- set readonly setting for selectedPoint
-- call api to get all points as user my have CRUD a new/existing point in backend
-- set node name as the bacnet point name
*/

/*
 on input updated send down new value
*/

/*
 pointsList: is all the points return from the backend api call
 point_object: is the whole point
 selected_point: selected point is to show the point select in the settings as read only
 topic: is the MQTT topic to subtribe from, The objectIdentifier form the topic, It is the combination of the point address and the objectType
*/


export class BACnetServerRest extends Node {
  //in nums
  inputFault = 0;
  inputOneNum = 1;
  inputTwoNum = 2;
  inputThreeNum = 3;

  //out nums
  outConnection = 0;
  outNum = 1;
  outJson = 2;

  //MQTT
  enable: boolean
  client: any;
  messageQueue: Array<MQTTMessage> = [];
  topic: any;
  rateLimitMS = 200;

  // bacnet
  selectedPoint:string
  pointName:string
  objectType:string
  address:number

  // for stopping sending same value twice to backend
  lastInput = null;
  lastSendTime;
  resendTime = Math.floor(Math.random() * 60) + 1;

  //ip
  host = '0.0.0.0'
  port = 1717

  enableDebug = false

  constructor() {
    super();
    this.title = NODE_INFO['protocols/bacnet-server']['bacnet-server-api'].title;
    this.description = NODE_INFO['protocols/bacnet-server']['bacnet-server-api'].description;
    this.properties['pointObject'] = null;
    this.settings['selected_point'] = NodeSetting.create({ description: 'Point Object Id', value: '', type: SettingType.READONLY });
    this.settings['selected_point_name'] = NodeSetting.create({ description: 'Point Name', value: '', type: SettingType.READONLY });
    this.settings['pointsList'] = NodeSetting.create({
      description: 'Points List',
      type: SettingType.AUTOCOMPLETE,
      config: {
        items: [],
      },
    });
    this.settings['enable'] = NodeSetting.create({ description: 'Enable', value: true, type: SettingType.BOOLEAN });
    this.addInput('fault', Type.BOOLEAN);
    this.addInput('in 1', Type.NUMBER);
    this.addInput('in 10', Type.NUMBER);
    this.addInput('in 16', Type.NUMBER);
    this.addOutput('connection', Type.ANY);
    this.addOutput('output', Type.ANY);
    this.addOutput('output-json', Type.JSON);
  }

  async setTopic() {
    const point_object = this.getObjectIdentifier()
    this.topic = `bacnet/server/points/ao/${point_object}`
  }

  getTopic() {
    return this.topic
  }

  getPointUUID() {
   const obj = this.getPointObject()
   if (isNull(obj)) return
   return obj.uuid
  }

  async getPoints() {
    await this.fetchPoints(this.host, this.port)
  }

  async setEnable() {
    this.enable = this.settings['enable'].value
  }

  getEnable() {
    return this.enable
  }


  getPointObject() {
    return this.properties['pointObject']
  }

  async setSelectedPoint() {
    const selectedPoint = this.getPointObject()
    if (isNull(selectedPoint)) return;
    const objectName = selectedPoint.object_name
    const objectType = selectedPoint.object_type;
    const address = selectedPoint.address;
    const objectIdentifier = `${objectType.substring(10)}-${address}`
    const cleanObjectType = `${objectType.substring(10)}`
    this.setAddress(address)
    this.setObjectType(cleanObjectType)
    this.setObjectIdentifier(objectIdentifier)
    this.pointName = `${objectIdentifier} -> ${objectName}`
    this.settings['selected_point_name'].value = objectName
    this.broadcastSettingsToClients();
  }

  getAddress() {
    return this.address
  }

  setAddress(address) {
    this.address = address
  }

  getObjectType() {
    return this.objectType
  }

  setObjectType(objectType) {
    this.objectType = objectType
  }


  getSelectedPoint() {
    return this.selectedPoint
  }

  setObjectIdentifier(objectIdentifier) {
    this.settings['selected_point'].value = objectIdentifier
  }


  getObjectIdentifier() {
    return this.settings['selected_point'].value
  }


  updateTitle() {
    this.title = `BACnet-Server: ${this.pointName}`;
    this.broadcastTitleToClients();
  }


   onExecute() {
    if (this.side !== Side.server) return;
    const pointValueIn1: number = this.getInputData(this.inputOneNum);
    const pointValueIn10 = this.getInputData(this.inputTwoNum);
    const pointValueIn16 = this.getInputData(this.inputThreeNum);
    this.doPointWrite(pointValueIn1, pointValueIn10, pointValueIn16)
  }



  inputChange() {
    if (this.side !== Side.server) return;
    const pointValueIn1: number = this.getInputData(this.inputOneNum);
    const pointValueIn10 = this.getInputData(this.inputTwoNum);
    const pointValueIn16 = this.getInputData(this.inputThreeNum);
    if (this.lastInput == null || pointValueIn16 !== this.lastInput){
      const uuid = this.getPointUUID()
      if(isNull(uuid))return 
      this.doPointWrite(pointValueIn1, pointValueIn10, pointValueIn16)
    }
  }

 
  doPointWrite(pointValueIn1, pointValueIn10, pointValueIn16){
    const host =  this.host
    const port = this.port
    // const uuid = this.getPointUUID()
    const address = this.getAddress()
    const objectType = this.getObjectType()
    if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE: doPointWrite ${[host, port, objectType, address, pointValueIn1, pointValueIn10, pointValueIn16]}`);
    if(isNull(address))return 
    if(isNull(objectType))return 
    this.writePointValue(host, port, objectType, address, pointValueIn1, pointValueIn10, pointValueIn16).then(e => {
      if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE: doPointWrite did HTTP post ${e}`);
    }).catch(err=> {
      if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE: doPointWrite tried HTTP POTS AND GOT ERROR ${err}`);

    })
  }


  onInputUpdated() {
    this.inputChange()

  }

  persistSettings() {
    if (!this.container.db) return;
    this.container.db.updateNode(this.id, this.container.id, {
      $set: {
        settings: this.settings,
        properties: this.properties,
      },
    });
  }

 async onAfterSettingsChange() {
    const pointsList = this.settings['pointsList'].value;
    await this.setEnable()
    this.properties['pointObject'] = pointsList
    await this.setSelectedPoint()
    await this.setTopic()
    const enable = this.getEnable()
    if (this.side == Side.server) {
      this.disconnectFromBroker();
      if (enable) {
        this.connectToBroker();
        if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE: connect to Broker from settings`);
      }
    }
    this.getPoints()
    this.updateTitle()
    this.inputChange()
  }

  onRemoved() {
    this.disconnectFromBroker();
  }

  async fetchPoints(host: string, port: number) {
    if (this.side !== Side.server) return;
    const url = `${Utils.buildUrl(host, port)}/api/bacnet/points`;
    const response = await axios.get(url);
    let res = response.data.points
    const points = res.map(point => {
      return { value: point, text: point.object_name };
    });
    this.settings['pointsList'].config.items = points;
    if(isNull(points)) return
    this.broadcastSettingsToClients();


  }

  async writePointValue(host: string, port: number, objectType: string, address: number, pointValueIn1:number, pointValueIn10:number, pointValueIn16:number): Promise<any> {
    if (this.side !== Side.server) return;
    const url = `${Utils.buildUrl(host, port)}/api/bacnet/points/obj/${objectType}/${address}`;
    this.lastInput = pointValueIn1;
    const body = {
      "priority_array_write": {
        // "_1": pointValueIn1,
        // "_10": pointValueIn10,
        "_16": pointValueIn16
      }
    }
    if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE:  writePointValue, url:${url} body:${body} host:${host} port:${port} lastInput:${this.lastInput}`);
    await axios.patch(url, body, {
      headers: {
        'Content-Type': 'application/json'
      },
    });
    return null;
  }


  onAdded() {
    this.onAfterSettingsChange() 
    if (this.side !== Side.server) return;
    const time = (this.resendTime * 1000) + 200 * 1000 //try and make the resend time a little more random
    this.EXECUTE_INTERVAL = time
  }


  processMessage(mqttMessage: MQTTMessage) {
    // look into using mqtt-match library. (https://www.npmjs.com/package/mqtt-match)
    const top = this.topic;
    if (matchMQTT(top, mqttMessage.topic)) {
      this.setOutputData(this.outNum, mqttMessage.message);
      this.setOutputData(this.outJson, mqttMessage);
    }
  }


  connectToBroker() {
    let { host, port, username, password } = config.mqtt;
    let options: any = {};
    //use use env
    if (isNull(host)) return;
    if (isNull(port)) return;
    if (isNull(this.getTopic())) return;
    if (this.getEnable() === false) return;
    options.host = host;
    options.port = port;

    this.disconnectFromBroker();
    this.client = mqtt.connect(options);
    this.client.on('connect', () => {
      if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE: this.client = mqtt.connect options: ${JSON.stringify(options)}`);
      this.setOutputData(this.outConnection, true, true);
      let topic = this.getTopic()
      // if no json topic use input 2 topic
      if (!isNull(topic)) {
        if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE:  subscribe on node input topic ${topic}`);
        this.client.subscribe(topic);
      }

    });
    this.client.on('close', () => {
      if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE:  MQTT CLIENT close WAS CALLED`);
      this.setOutputData(this.outConnection, false, true);
    });

    this.client.on('error', error => {
      this.debugWarn(error);
    });
    this.client.on('message', (topic, message) => {
      let obj: MQTTMessage = { topic: topic, message: message.toString() };
      if (this.enableDebug) this.debugInfo(`BACNET-SERVER NODE:  this.client.on('message' ${JSON.stringify(obj)}`);
      this.processMessage(obj);
    });
  }

  disconnectFromBroker() {
    if (this.client) this.client.end();
  }



}
Container.registerNodeType('protocols/bacnet-server/bacnet-server-api', BACnetServerRest);
