import { NodeTypeRegistry } from '../../node-type-registry';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import { NODE_INFO } from '../../node-info';

class DPAirflowNode extends Node {
  constructor() {
    super();
   
    this.title = NODE_INFO.hvac["dp-airflow"].title;
    this.description = NODE_INFO.hvac["dp-airflow"].description;
   
    this.addInput('ductArea(m²)', Type.NUMBER);
    this.addInput('diffPressure(Pa)', Type.NUMBER);
    this.addInput('Kfactor', Type.NUMBER);
    this.addOutput('airflow(L/s)', Type.NUMBER);
    this.addOutput('airflow(cfm)', Type.NUMBER);

    this.settings['areaUnits'] = NodeSetting.create({
      description: 'Duct Area Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: true, text: 'Square Meters (m²)' },
          { value: false, text: 'Square Feet (ft²)' },
        ],
      },
      value: true,
    });

    this.settings['DPUnits'] = NodeSetting.create({
      description: 'Differential Pressure Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: true, text: 'Pascals (Pa)' },
          { value: false, text: 'Inches of Water (in. w.g.)' },
        ],
      },
      value: true,
    });
  }

  onAdded() {
    this.setOutputData(0, null);
  }

  onInputUpdated() {
    this.onInputOrSettingsChange();
  }

  onAfterSettingsChange() {
    this.updateTitles();
    this.onInputOrSettingsChange();
  }

  private onInputOrSettingsChange() {
    var area = this.getInputData(0);
    var DP = this.getInputData(1);
    var KFactor = this.getInputData(2);
    if (area == null || DP == null || KFactor == null) {
      this.setOutputData(0, null);
      return;
    }

    if (this.settings['areaUnits'].value) area = area * 10.7639;
    if (this.settings['DPUnits'].value) DP = DP * 0.00401865;
    

    //Calculation reference https://cgproducts.johnsoncontrols.com/MET_PDF/6375120.PDF page 30
    const airflowCFM = (4005 * area) * Math.sqrt(DP/KFactor);
    const airflowLS = airflowCFM * 0.4719474;

    this.setOutputData(0, airflowLS.toFixed(2));
    this.setOutputData(1, airflowCFM.toFixed(2));
  }

  updateTitles(){
    if (this.settings['areaUnits'].value) this.inputs[0].name = 'ductArea(m²)';
    else this.inputs[0].name = 'ductArea(ft²)';
    if (this.settings['areaUnits'].value) this.inputs[1].name = 'diffPressure(Pa)';
    else this.inputs[1].name = 'diffPressure(in. w.g)';
  }
}
NodeTypeRegistry.registerNodeType('hvac/dp-airflow', DPAirflowNode);
