import { NodeFactory } from '../../nodes/node-factory';
import {
  ContainerRegistryMetadata,
  IContainer,
  IFolderNode,
  INode,
  isFolderNode,
  SerializedContainer,
  SerializedNode,
} from '../../nodes/node-interface';
import { NodeRegistryHelper } from '../../nodes/node-registry';
import { CreationResult, initETLResult, RemoveResult } from '../../shared/etl-result';
import { ContainerRegistryValidator } from '../../shared/validator';
import { isNotNull } from '../../utils/helper';
import { IdGenerator } from '../../utils/id-generator';
import { NodeService } from './node-service';

export class NodeCRUDService extends NodeService {

  getNodes(parent: number | string | IContainer, registry?: ContainerRegistryMetadata): Promise<SerializedContainer> {
    const reg = registry ?? this.cRegistry;
    const db = reg.rootContainer()['db'];
    return ContainerRegistryValidator.mustExisted(parent, reg)
                                     .then(c => this.dbHelper.listByContainer(db, c.id)
                                                    .then(nodes => this.getNodesInContainer(db, nodes))
                                                    .then(nodes => <SerializedContainer>{
                                                      id: c.id,
                                                      name: c.name,
                                                      serializedNodes: nodes,
                                                    }));
  }

  createNode(parent: number | string | IContainer, node: SerializedNode,
             registry?: ContainerRegistryMetadata): Promise<CreationResult> {
    this.logger.info(`Creating new node '${node.type}' in container '${parent}'...`);
    const reg = registry ?? this.cRegistry;
    return ContainerRegistryValidator.mustExisted(parent, reg)
                                     .then(p => p.nodeRegistry().register(NodeFactory.create(node, true, registry)))
                                     .then(n => new CreationResult(n));
  }

  removeNodes(parent: number | string | IContainer, nodes: (number | string | SerializedNode | INode)[],
              recursive: boolean = false, metadata?: ContainerRegistryMetadata): Promise<RemoveResult> {
    const registry = metadata ?? this.cRegistry;
    const db = registry.rootContainer()['db'];
    const v = ContainerRegistryValidator.mustExisted(parent, registry);
    return v.then(p => {
      const findNodes = this.findNodes(p, nodes, recursive, registry);
      return this.dbHelper.batchDeleteByIds(db, findNodes.map(n => IdGenerator.createNodeId(n.cid, n.id)))
                 .then(_ => NodeRegistryHelper.unregister(p, findNodes, registry, true))
                 .then(r => initETLResult<RemoveResult>(RemoveResult, p.id, findNodes.length, r));
    });
  }

  private findNodes(p: IContainer, nodes: (number | string | SerializedNode | INode)[], recursive: boolean,
                    registry: ContainerRegistryMetadata): SerializedNode[] {
    const list = NodeFactory.standardize(p, nodes);
    if (!recursive) {
      return list;
    }
    return list.flatMap(node => {
      const n = p.nodeRegistry().get(node);
      if (isFolderNode(n)) {
        const container = registry.get((<IFolderNode>n).sub_container.id);
        if (isNotNull(container)) {
          return [...this.findNodes(container, container.nodeRegistry().list(), recursive, registry), n];
        }
      }
      return [n ?? node];
    });
  }
}
