import config from '../../../config';
import { isNull } from '../../../utils/helper';
import { NodeTypeRegistry } from '../../node-type-registry';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import PlatformDataSource from '../../../database/datasource/PlatformDataSource';

let moment = require('moment-timezone');
const {InfluxDB, Point, HttpError} = require('@influxdata/influxdb-client');
const {SetupAPI} = require('@influxdata/influxdb-client-apis')

class InfluxDB2Node extends Node {
  inEnable: number = 0;
  inJsonInput: number = 1;
  outError: number = 0;
  outLastExport: number = 1;
  outMsg: number = 2;
  client;
  lastJSON;
  constructor() {
    super();
    this.title = NODE_INFO.history["influx-db-2"].title;
    this.description = NODE_INFO.history["influx-db-2"].description;
    this.addInputWithSettings('enable', Type.BOOLEAN, false, 'Enable');
    this.addInput('json-input', Type.JSON);
    this.addOutput('error', Type.BOOLEAN);
    this.addOutput('outLastExport', Type.STRING);
    this.addOutput('outMsg', Type.STRING);
    this.settings['useDefaultTags'] = NodeSetting.create({ description: 'useDefaultTags', value: false, type: SettingType.BOOLEAN });
    this.settings['dontUseEnv'] = NodeSetting.create({ description: 'Dont use env settings', value: false, type: SettingType.BOOLEAN });
    this.settings['url'] = NodeSetting.create({ description: 'URL', value: '', type: SettingType.STRING });
    this.settings['token'] = NodeSetting.create({ description: 'Token', value: '', type: SettingType.STRING });
    this.settings['org'] = NodeSetting.create({ description: 'Org', value: '', type: SettingType.STRING });
    this.settings['bucket'] = NodeSetting.create({ description: 'Bucket', value: '', type: SettingType.STRING });

    this.setSettingsConfig({
      groups: [
        //{ host: { weight: 3 }, port: { weight: 1 } },
        //{ user: {}, password: {} },
        { period: { weight: 2 }, periodUnits: {} },
      ],
      conditions: {
        url: setting => {
          return !!setting['dontUseEnv'].value;
        },
        token: setting => {
          return !!setting['dontUseEnv'].value;
        },
        org: setting => {
          return !!setting['dontUseEnv'].value;
        },
        bucket: setting => {
          return !!setting['dontUseEnv'].value;
        },
      },
    });
  }
  
  onAdded() {
  }

  createClient() {
    let {url, token, org, bucket} = config.influxDb2;
    if (this.settings['dontUseEnv'].value) {
      // dont use env
      url = this.settings['url'].value;
      token = this.settings['token'].value;
      org = this.settings['org'].value;
      bucket = this.settings['bucket'].value;
    } 
    this.client = new InfluxDB({url: url, token: token}).getWriteApi(org, bucket);
  }

  onInputUpdated() {
    if (this.side !== Side.server) return; // only execute on server
    if (!this.getInputData(this.inEnable)) {
      this.setOutputData(this.outMsg, `INFLUX-DB-2 NODE: not enabled`, true);
      return;
    }

    // update node name - UPDATE TITLE FROM JSON INSTEAD
    /*
    if (this.inputs[0].updated) {
      let nodeName = this.getInputData(this.inName);
      if (!isNull(nodeName)) {
        this.name = nodeName;
        this.broadcastNameToClients();
      }
    }
    */

    let input = this.getInputData(this.inJsonInput);
    //console.log('INFLUX-DB-2: input = ', input)

    if (isNull(input)) return;
    const hasMeasurement = input.hasOwnProperty('measurement');
    const hasMeasurementTags = input.hasOwnProperty('tags');
    const hasMeasurementFields = input.hasOwnProperty('fields');

    if (isNull(input.tags)) {
      this.setOutputData(this.outError, false, true);
      this.setOutputData(this.outMsg, `!tags! is empty,`, true);
      return;
    }
    if (isNull(input.measurement)) {
      this.setOutputData(this.outError, false, true);
      this.setOutputData(this.outMsg, `!measurement! is empty,`, true);
      return;
    }
    if (isNull(input.fields)) {
      this.setOutputData(this.outError, false, true);
      this.setOutputData(this.outMsg, `!fields! is empty,`, true);
      return;
    }
    if (hasMeasurement && hasMeasurementTags && hasMeasurementFields) {
      this.setOutputData(this.outError, false, true);
      this.setOutputData(this.outLastExport, moment().format());
      this.setOutputData(this.outMsg, `influx write to DB`, true);

      Object.keys(input.tags).forEach(key => {
        if (!input.tags[key]) {
          delete input.tags[key];
        }
      });

      PlatformDataSource.getPlatform().then(e => {
        var platformInfo = e;
        this.setOutputData(2, e)
      }).catch(err => {
        this.debugInfo(`INFLUXDB-2 NODE: try/catch  getPlatform info, ${err}`);
      })

      this.createClient();

      if (this.settings['useDefaultTags'].value) this.client.useDefaultTags({location: 'test'})

      const point = new Point(input.measurement)
      point.timestamp(new Date());
      for (const tag in input.tags) point.tag(tag, input.tags[tag]);
      for (const field in input.fields) {
        switch (typeof input.fields[field]){
          case 'number':
            //console.log('its a number')
            point.floatField(field, input.fields[field])
            break;
          case 'string':
            //console.log('its a string')
            point.stringField(field, input.fields[field])
            break;
          case 'boolean':
            //console.log('its a boolean')
            point.booleanField(field, input.fields[field])
            break;
          default:
            //console.log('I dont know its type')
            if(input.fields[field] == null) break;
            point.floatField(field, input.fields[field])
            break;
        }
      }
      this.client.writePoint(point);
      //console.log(`INFLUXDB-2: ${point.toLineProtocol(this.client)}`);

      this.client.close()
        .then(() => {
          //console.log('SUCCESS')
        })
        .catch(e => {
          console.error(e)
          if (e instanceof HttpError && e.statusCode === 401) {
            console.log('INFLUXDB-2: Run ./onboarding.js to setup a new InfluxDB database.')
          }
          console.log('\n INFLUXDB-2:  Finished ERROR')
          this.setOutputData(this.outError, true, true);
          this.setOutputData(this.outMsg, `error on influx write, ${e}`, true);
        })
    } else {
      this.debugInfo(`INFLUX-DB NODE: input.hasOwnProperty is missing`);
      this.setOutputData(this.outError, true, true);
      this.setOutputData(this.outMsg, `input.hasOwnProperty is missing`, true);
    }
  }

  onAfterSettingsChange() {
    if (this.side !== Side.server) return; // only execute on server
    /*
    if (this.getInputData(this.inEnable)) this.createClient();
    else {
      if (this.client) this.client.dispose()
        .catch(e => {
          console.log(`INFLUX-DB-2:`, e)
        })
      this.client = null;
      this.setOutputData(this.outMsg, `INFLUX-DB-2 NODE: not enabled`, true);
      return;
    }
    */
    this.onInputUpdated();
  }
}
NodeTypeRegistry.registerNodeType('history/influx-db-2', InfluxDB2Node);

/*
influxDb2    : {
    protocol    : env?.INFLUX_DB_2_PROTOCOL ?? 'http',
    url         : env?.INFLUX_DB_2_URL ?? '',
    token       : env?.INFLUX_DB_2_TOKEN ?? '',
    bucket      : env?.INFLUX_DB_2_BUCKET ?? '',
    org         : env?.INFLUX_DB_2_ORG ?? '',
    username    : env?.INFLUX_DB_2_USERNAME ?? '',
    password    : env?.INFLUX_DB_2_PASSWORD ?? '',
  },
*/