import {
  ContainerRegistryMetadata,
  IContainer,
  INode,
  isSerializedContainer,
  SerializedContainer,
} from '../nodes/node-interface';
import { NotFoundError } from '../utils/errors';
import { isNull } from '../utils/helper';

const Joi = require('@hapi/joi');

export class NodeValidator {
  static NodeArrayValidator() {
    return Joi.array().items(Joi.object().keys({ type: Joi.string().required() }));
  }
}

export interface IContainerRegistryValidator {

  validate(cid: number | string | SerializedContainer | IContainer, strict?: boolean): IContainer;

}

export interface INodeRegistryValidator {

  validate(node: number | string | INode, container: IContainer, strict?: boolean): INode;

}

export class NodeRegistryValidator implements INodeRegistryValidator {

  validate(node: number | string | INode, container: IContainer, strict: boolean = true): INode {
    const n = container.nodeRegistry().get(node);
    if (isNull(n) && strict) {
      throw new NotFoundError(`Not found node id ${node} in container ${container.id}`);
    }
    return n;
  }

}

export class ContainerRegistryValidator implements IContainerRegistryValidator {

  private readonly registry: ContainerRegistryMetadata;

  constructor(registry: ContainerRegistryMetadata) {
    this.registry = registry;
  }

  static mustExisted = (cid: number | string | SerializedContainer | IContainer,
                        registry: ContainerRegistryMetadata): Promise<IContainer> => {
    return Promise.resolve(() => new ContainerRegistryValidator(registry).validate(cid)).then(f => f());
  };

  validate(cid: number | string | SerializedContainer | IContainer, strict: boolean = true): IContainer {
    const container = this.registry.get(isSerializedContainer(cid) ? (<SerializedContainer>cid).id : cid + '');
    if (isNull(container) && strict) {
      throw new NotFoundError(`Not found container id ${cid}`);
    }
    return container;
  }

}

export class ListValidator {

  static notEmpty = <T>(items: T[], msg: string): Promise<T[]> => {
    if (items.length > 0) {
      return Promise.resolve(items);
    }
    return Promise.reject(msg);
  };

}
