import { Database } from '../database/database';
import { ContainerRegistry } from '../nodes/container';
import { createNodeDbHelper, NodeDbHelper } from '../nodes/node-db-helper';
import { ContainerRegistryMetadata, IContainer, INode } from '../nodes/node-interface';
import { SlimNodeIO } from '../nodes/node-link';
import Utils from '../nodes/utils';
import { isFunction, isNotNull, isNull } from '../utils/helper';
import { LinkResult } from './link-result';
import { ContainerRegistryValidator, IContainerRegistryValidator, NodeRegistryValidator } from './validator';

export interface LinkInputVisitor {
  visit(c: IContainer, n: INode, ioKey: string, io: SlimNodeIO): void;
}

export interface LinkOutputVisitor {
  visit(c: IContainer, n: INode, ioKey: string, ios: SlimNodeIO[]): void;
}

export interface LinkNodeVisitor<V extends any | Promise<any>> {
  visit(c: IContainer, n: INode): V;
}

interface LinkContainerVisitor {
  visit(c: IContainer): void;
}

export interface LinkResultVisitor<T extends LinkResult | Promise<LinkResult>> {

  readonly registry: ContainerRegistryMetadata;

  inputVisitor(): LinkInputVisitor;

  outputVisitor(): LinkOutputVisitor;

  nodeVisitor(): LinkNodeVisitor<any | Promise<any>>;

  containerVisitor(): LinkContainerVisitor;

  visit(result: LinkResult): T;

}

class BaseNodeLinkVisitor<V extends any | Promise<any>> implements LinkNodeVisitor<V> {

  private readonly fn: (c: IContainer, n: INode) => V;

  constructor(fn?: (c: IContainer, n: INode) => V) {
    this.fn = fn;
  }

  visit(c: IContainer, n: INode): V {
    n.isUpdated = true;
    return isFunction(this.fn) ? this.fn(c, n) : null;
  }
}

abstract class DefaultLinkVisitor<T extends LinkResult | Promise<LinkResult>> implements LinkResultVisitor<T> {

  readonly registry: ContainerRegistryMetadata;
  protected readonly validator: IContainerRegistryValidator;

  protected constructor(registry?: ContainerRegistryMetadata) {
    this.registry = registry ?? ContainerRegistry.instance();
    this.validator = new ContainerRegistryValidator(this.registry);
  }

  abstract nodeVisitor(): LinkNodeVisitor<void | Promise<void>>;

  abstract visit(result: LinkResult): T;

  containerVisitor(): LinkContainerVisitor {
    return new class implements LinkContainerVisitor {
      visit(c: IContainer): void {
      }
    };
  }

  inputVisitor(): LinkInputVisitor {
    return new class implements LinkInputVisitor {
      visit(c: IContainer, n: INode, ioKey: string, io: SlimNodeIO): void {
        n.inputs[ioKey].link = io;
        n.inputs[ioKey].updated = true;
        if (isNotNull(io)) {
          n.inputs[ioKey].data = Utils.parseValue(c.nodeRegistry().get(io.nodeId)?.outputs[io.ioId]?.data,
                                                  n.inputs[ioKey].type);
        }
      }
    };
  }

  outputVisitor(): LinkOutputVisitor {
    return new class implements LinkOutputVisitor {
      visit(c: IContainer, n: INode, ioKey: string, ios: SlimNodeIO[]): void {
        if (isNotNull(n.outputs[ioKey])) {
          n.outputs[ioKey].links = ios;
        }
      }
    };
  }

  protected perNode(d: { nid: number | string; inputs: { [p: string]: SlimNodeIO }; outputs: { [p: string]: SlimNodeIO[] } },
                    c: IContainer): Promise<any> | any {
    const n = new NodeRegistryValidator().validate(d.nid, c, false);
    if (isNull(n)) {
      return;
    }
    const inputVisitor = this.inputVisitor();
    const outputVisitor = this.outputVisitor();
    Object.keys(d.inputs).forEach(k => inputVisitor.visit(c, n, k, d.inputs[k]));
    Object.keys(d.outputs).forEach(k => outputVisitor.visit(c, n, k, d.outputs[k]));
    return this.nodeVisitor().visit(c, n);
  }

}

export class BackendLinkVisitor extends DefaultLinkVisitor<Promise<LinkResult>> {
  private readonly dbHelper: NodeDbHelper;
  private readonly db: Database;

  constructor(registry?: ContainerRegistryMetadata, dbHelper?: NodeDbHelper) {
    super(registry);
    this.dbHelper = dbHelper ?? createNodeDbHelper();
    this.db = this.registry.rootContainer()['db'];
  }

  nodeVisitor(): LinkNodeVisitor<Promise<any>> {
    return new BaseNodeLinkVisitor((c, n) => this.dbHelper.updateAsync(this.db, n, 'inputs', 'outputs'));
  }

  visit(result: LinkResult): Promise<LinkResult> {
    return Promise.all(Object.keys(result.result())
                             .map(cid => this.validator.validate(cid, false))
                             .filter(c => isNotNull(c))
                             .map(c => Promise.all(Object.values(result.result()[c.id]).map(d => this.perNode(d, c)))
                                              .then(_ => this.containerVisitor().visit(c))))
                  .then(_ => result);
  }

}

export class FrontendLinkVisitor extends DefaultLinkVisitor<LinkResult> {

  private readonly fn: (c: IContainer) => void;

  constructor(fn: (c: IContainer) => void, registry?: ContainerRegistryMetadata) {
    super(registry);
    this.fn = fn;
  }

  nodeVisitor(): LinkNodeVisitor<void> {
    return new BaseNodeLinkVisitor<void>();
  }

  containerVisitor(): LinkContainerVisitor {
    const that = this;
    return new class implements LinkContainerVisitor {
      visit(c: IContainer): void {
        that.fn(c);
      }
    };
  }

  visit(result: LinkResult): LinkResult {
    Object.keys(result.result())
          .map(cid => this.validator.validate(cid, false))
          .filter(c => isNotNull(c))
          .forEach(c => {
            Object.values(result.result()[c.id]).forEach(d => this.perNode(d, c));
            this.containerVisitor().visit(c);
          });
    return result;
  }

}

