import axios from 'axios';
import { Container } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { Side } from '../../node-interface';
import { NodeSetting, SettingType, Type } from '../../node-io';
import SystemUtils from '../system/system-utils';
import Utils from '../../utils';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';
import { bsaClientConfig } from './bsa-client-config';
import PlatformDataSource from '../../../database/datasource/PlatformDataSource';

class BSACumulocityOperationNode extends Node {
  lastTrigger;
  timeoutFunc;

  //inputs
  ENABLESLOT = 0;
  TRIGGERSLOT = 1;

  //outputs
  OUTPUTSTARTSLOT = 3;

  constructor() {
    super();
    this.title = NODE_INFO.cumulocity["cumulocity-operation"].title;
    this.description =  NODE_INFO.cumulocity["cumulocity-operation"].description;

    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInput('trigger', Type.BOOLEAN);

    this.addOutput('error', Type.BOOLEAN);
    this.addOutput('message', Type.STRING);
    this.addOutput('numExecuting', Type.NUMBER);

    this.settings['CumulocityDeviceID'] = NodeSetting.create({
      description: 'Cumulocity Device ID (Number)',
      value: '',
      type: SettingType.STRING,
    });
    this.settings['interval'] = NodeSetting.create({
      description: 'Update Interval (min 15 seconds)',
      type: SettingType.NUMBER,
      value: 1,
    });
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: TIME_TYPE.SECONDS, text: 'Seconds' },
          { value: TIME_TYPE.MINUTES, text: 'Minutes' },
          { value: TIME_TYPE.HOURS, text: 'Hours' },
        ],
      },
      value: TIME_TYPE.MINUTES,
    });
    this.settings['operationsCount'] = NodeSetting.create({
      description: 'Number of Operations',
      value: 1,
      type: SettingType.NUMBER,
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  init(){
    this.EXECUTE_INTERVAL = Math.max(15000, TimeUtils.timeConvert(this.settings['interval'].value, this.settings['time'].value));
    this.properties['operationsCount'] = 0;
    this.changeTopicsCount(this.settings['operationsCount'].value);
    this.setOutputData(this.OUTPUTSTARTSLOT, null);
  }

  onCreated(){
  }

  onAdded() {
    if (this.side !== Side.server) return;
    this.EXECUTE_INTERVAL = Math.max(15000, TimeUtils.timeConvert(this.settings['interval'].value, this.settings['time'].value));
    this.restoreOperationValues()
    this.onAfterSettingsChange();
  }

  async onAfterSettingsChange() {
    if (this.side !== Side.server) return;
    //await this.createOperation();
    let operationsCount = this.settings['operationsCount'].value;
    operationsCount = Utils.clamp(operationsCount, 1, 100);
    this.changeTopicsCount(operationsCount);
    this.renameOutputs();
    this.broadcastOutputsToClients();
    this.checkOperationsExist();
    this.checkOperationsExecuting();
    this.storeOperationValues();
  }

  async onInputUpdated() {
    if (this.side !== Side.server) return;
    if (this.getInputData(this.ENABLESLOT)) {
      if(this.getInputData(this.TRIGGERSLOT) && this.inputs[this.TRIGGERSLOT]){
        this.checkOperationsExecuting();
      }
    }
  }

  onExecute(){
    console.log('onExecute()')
    if (this.getInputData(0)) {
      this.checkOperationsExecuting();
    }
  }

  async checkOperationsExist(){
    const ALLoperations = await this.getDeviceOperations('ALL');
    console.log('ALLoperations', ALLoperations)
    const oldOperationsArray = this.properties['operationsArray'] || [];
    const operationsArray = this.makeOperationsArray();
    console.log('OPERATIONS ARRAY', operationsArray)
    this.setNewOperationsToNull(oldOperationsArray, operationsArray);
    if(operationsArray.length > 0){
      operationsArray.forEach(async (operationName:any, index:number)=>{
        if(operationName != undefined && operationName != 'undefined' && operationName != ''){
          const exists = ALLoperations.some((anyOperation:any)=>{
            return anyOperation['c8y_control']['measurementName'] == operationName;
          });
          if(!exists) {
            console.log(`CREATING OPERATION ${operationsArray[index]}`)
            await this.createOperation(operationsArray[index]);
            this.setOutputData(this.OUTPUTSTARTSLOT + index, null)
          }
        }
      });
    }
  }

  async createOperation(operationName:string) {
    const deviceId = this.settings['CumulocityDeviceID'].value;

    let cfg: any = bsaClientConfig('operation');
    cfg = {
      ...cfg,
      method: 'POST',
      data: {
        deviceId: deviceId,
        deviceName: this.properties['CumulocityDeviceName'],
        c8y_control: {
          originalValue:"", 
          assetName: "Nube-io Wires",
          measurementName: operationName,
          changedValue: "" 
        },
        description: operationName
      }
    };
    try {
      await axios(cfg)
        .then(function (response) {
          //console.log('AXIOS Response: ', response)
        });
    } catch (error) {
      this.setOutputData(0, true);
      this.setOutputData(1, String(error));
      console.log('AXIOS Response: ', error)
    }
  }

  makeOperationsArray(){
    let operationsArray = [];
    for(var i=1; i<= this.properties['operationsCount']; i++){
      operationsArray.push(this.settings['operation'+i].value);
    }
    this.properties['operationsArray'] = operationsArray;
    return operationsArray;
  }

  async checkOperationsExecuting(){
    const operationsArray = this.makeOperationsArray();
    const foundOperations = await this.getDeviceOperations('EXECUTING');
    this.setOutputData(2, foundOperations.length);
    console.log('EXECUTING OPERATIONS', foundOperations)
    if(foundOperations.length > 0){
      foundOperations.forEach(async (operation:any)=>{
        var measurement = operation['c8y_control']['measurementName'];
        const data = operation['c8y_control']['changedValue'];
        const id = operation['id'];
        var index = -1;
        if(operationsArray.includes(measurement)){
          index = operationsArray.indexOf(measurement);
        }
        if(index >= 0){
          this.setOutputData(this.OUTPUTSTARTSLOT + index, data)
          await this.operationSuccess(id);
        }
      });
    }
    this.storeOperationValues();
  }

  async getDeviceOperations(operationType:string) {
    if(operationType != 'EXECUTING' && operationType != 'PENDING' && operationType != 'ALL') return [];
    let deviceId = this.settings['CumulocityDeviceID'].value;
    let operations = [];
    let cfg: any = bsaClientConfig('operation');
    cfg.headers = {}
    cfg = {
      ...cfg,
      method: 'GET',
      //url: `${cfg.url}?deviceId=${deviceId}`,
      //url: `${cfg.url}?status=PENDING&deviceId=${deviceId}`,
      //url: `${cfg.url}?status=EXECUTING&deviceId=${deviceId}`,
    };
    if(operationType == 'EXECUTING') cfg.url = `${cfg.url}?status=EXECUTING&deviceId=${deviceId}`;
    else if(operationType == 'ALL') cfg.url = `${cfg.url}?deviceId=${deviceId}`;

    let error = false;
    let errors = [];
    this.properties[`${operationType}operations`] = [];
    try {
      var next = cfg;
      do {next = await this.readNextOperation(this, next, operationType);}
      while(next);
    } catch (error) {
      this.setOutputData(0, true);
      this.setOutputData(1, String(error));
    }
    return this.properties[`${operationType}operations`];
  }

  async readNextOperation(self, cfg:any, operationType:string){
    if(operationType != 'EXECUTING' && operationType != 'PENDING' && operationType != 'ALL') return false;
    var result = false;
    var nextLink = ''
    //console.log('CFG', cfg);
    await axios(cfg)
      .then(async function (response) {
        //console.log('AXIOS Response: ', response);
        if(response.data.hasOwnProperty('operations') && response.data.operations.length > 0){
          self.properties[`${operationType}operations`] = self.properties[`${operationType}operations`].concat(response.data['operations']);
          nextLink = 'https://bsaautomation' + response.data.next.slice(response.data.next.indexOf('.apj.cumulocity.com'));
          cfg = {
            ...cfg,
            method: 'GET',
            url: nextLink,
          };
          result = cfg;
        }
        else result = false;
      })
      .catch(function (error) {
        console.log('AXIOS Error: ', error);
        throw(error);
      });
    return result;
  }

  async operationSuccess(id:string) {
    let deviceId = this.settings['CumulocityDeviceID'].value;
    let cfg: any = bsaClientConfig('operation');
    cfg = {
      ...cfg,
      method: 'PUT',
      data: {
        "status": "SUCCESSFUL"
      },
    };
    cfg.url = cfg.url + '/' + id;
    try {
      await axios(cfg)
        .then(function (response) {
          //console.log('AXIOS Response: ', response)
        });
    } catch (error) {
      this.setOutputData(0, true);
      this.setOutputData(1, String(error));
      console.log('AXIOS Response: ', error)
    }
  }

  restoreOperationValues(){
    this.properties['operationsValues'].forEach((value:any, index:number)=>{
      this.setOutputData(this.OUTPUTSTARTSLOT + index, value);
    });
  }

  storeOperationValues(){
    let operationValues = [];
    this.properties['operationsArray'].forEach((operationName:any, index:number)=>{
      if(this.outputs[this.OUTPUTSTARTSLOT + index].hasOwnProperty('data')){
        operationValues.push(this.outputs[this.OUTPUTSTARTSLOT + index].data);
      }
      else operationValues.push(null);
    });
    this.properties['operationsValues'] = operationValues;
    this.persistProperties(true, true);
  }

  setNewOperationsToNull(oldOperationsArray, operationsArray){
    operationsArray.forEach((operation:any, index:number)=>{
      if(index >= oldOperationsArray.length || oldOperationsArray[index] != operation) this.setOutputData(this.OUTPUTSTARTSLOT + index, null); 
    });
  }

  changeTopicsCount(target_count) {
    this.properties['operationsCount'] = this.getOutputsCount() - this.OUTPUTSTARTSLOT;
    const diff = target_count - this.properties['operationsCount'];
    if (diff == 0) return;

    //add inputs, outputs
    this.changeOutputsCount(target_count + this.OUTPUTSTARTSLOT, Type.ANY);

    //change settings for topics
    if (diff > 0) {
      for (let i = this.properties['operationsCount'] + 1; i <= target_count; i++) {
        this.settings['operation' + i] = NodeSetting.create({
          description: 'Operation ' + i,
          value: '',
          type: SettingType.STRING,
        });
      }
    } else if (diff < 0) {
      for (let i = this.properties['operationsCount']; i > target_count; i--) {
        delete this.settings['operation' + i];
      }
    }
    this.properties['operationsCount'] = target_count;
    //this.broadcastSettingsToClients();
  }

  renameOutputs() {
    for (let i = 0; i <= this.properties['operationsCount'] - 1; i++) {
      let topic = this.settings['operation' + (i + 1)]?.value || 'undefined';
      if (topic?.length > 20) topic = '...' + topic.substr(topic.length - 20, 20);
      this.outputs[String(i + this.OUTPUTSTARTSLOT)].name = '' + (i + 1) + ' | ' + topic;
    }
  }

}

Container.registerNodeType('cumulocity/cumulocity-operation', BSACumulocityOperationNode);
