import { Backend } from './backend/backend';
import conf from './config';
import * as config from './config.json';
import { AsyncDbHelper, Database } from './database/database';
import { Container, ContainerRegistry } from './nodes/container';
import { Node } from './nodes/node';
import { NodeHookHelper } from './nodes/node-hook';
import { SerializedNode, Side } from './nodes/node-interface';
import { NodeRegistryHelper } from './nodes/node-registry';
import { NodeTypeRegistry } from './nodes/node-type-registry';
import { RegistryResult } from './nodes/registry';
import { Dashboard } from './server/dashboard';
import { Server } from './server/server';
import { NodeTree } from './server/service/NodeETLService';
import { isEmpty } from './utils/helper';
import { NoChangeId } from './utils/id-generator';
// @formatter:on


export class App {
  db: Database;
  readonly dashboard: Dashboard;
  readonly server: Server;
  private readonly logger: Logger;

  constructor() {
    this.logger = create('app', { color: Colors.Green });
    this.logger.info('----------------------------- Rubix Wires -----------------------------');
    this.logger.info(`Process Dir       : '${process.cwd()}'`);
    this.logger.info(`Working Dir       : '${(<any>global).__rootdirname}'`);
    this.logger.info(`Data Dir          : '${process.env.DATA_DIR}'`);
    this.logger.info(`Environment       : '${process.env.NODE_ENV || 'development'}'`);
    this.logger.info(`Environment file  : '${envFileAbsPath}'`);
    this.logger.info('-----------------------------------------------------------------------');
    this.registerNodeTypes();
    this.server = new Server((<any>global).__rootdirname, conf.port, conf.loopInterval);
    this.dashboard = new Dashboard(this.server.dashboardSocket);
    Promise.resolve(ContainerRegistry.instance().register(Container.initRoot(Side.server), true))
           .then(rootContainer => this.start(rootContainer))
           .then(_ => this.logger.info(`Rubix Wires is ready`));
  }

  private start(rootContainer: Container) {
    return this.connectDatabase()
               .then(db => (this.db = db))
               .then(db => db.asyncLoadDatabase(conf.dataDir, conf.database.compactionInterval * 1000).then(_ => db))
               .then(db => this.onDatabaseReady(rootContainer, db))
               .catch(err => this.onDatabaseError(rootContainer, err));
  }

  private registerNodeTypes() {
    require('./nodes/nodes/index');
    this.logger.info(`Registered ${NodeTypeRegistry.instance().size()} nodes types`);
  }

  private connectDatabase(): Promise<Database> {
    this.logger.info(`Loading database...`);
    if (!config.dataBase.enable) {
      return Promise.reject(new Error('Not enabled database'));
    }
    if (!config.dataBase.useInternalDb) {
      return Promise.reject(new Error('Unsupported external database'));
    }
    return Promise.resolve(require('./database/neDbDatabase').db);

  }

  private startBackend() {
    new Backend(this.db, conf.backendSync).start();
  }

  private onDatabaseReady(rootContainer: Container, db: Database): Promise<any> {
    return Promise.resolve(this.configApp(rootContainer, this.server, this.dashboard, db))
                  .then(_ => AsyncDbHelper.noArgs<SerializedNode[]>(db, db.getNodes))
                  .then(nodes => new NodeTree(rootContainer.id, new NoChangeId()).compute(nodes).flatten())
                  .then(nodes => this.registerNodes(rootContainer, nodes))
                  .then(_ => this.dashboard.loadFromDatabase(db))
                  .then(_ => this.startBackend())
                  .then(_ => rootContainer.run(conf.loopInterval));
  }

  private registerNodes(rootContainer: Container, nodes: SerializedNode[]): Promise<RegistryResult<Node>> {
    return NodeRegistryHelper.register(rootContainer, nodes, ContainerRegistry.instance(), true)
                             .then(r => {
                               logger.info(`Imported ${ContainerRegistry.instance().size() - 1} containers, ` +
                                           `${r.objects.length}/${nodes.length} nodes successfully from database`);
                               if (!isEmpty(r.errors)) {
                                 logger.warn(`Import failed: [${r.errors.join('\n')}]`);
                               }
                               return r;
                             })
                             .then(r => {
                               r.objects.forEach(node => NodeHookHelper.execute(node, 'onDbReaded') ?? node);
                               return r;
                             });
  }

  private onDatabaseError(rootContainer: Container, err: Error) {
    if (err.message === 'Not enabled database') {
      //TODO is it need? Should remove it
      this.logger.warn(`Database is not enabled, rubix-wires is running on memory...`);
      rootContainer.run(conf.loopInterval);
      return;
    }
    this.logger.error(`Unable to bootstrap application. Failed at: ${err.stack ?? err}`);
    process.exit(99);
  }

  private configApp(rootContainer: Container, server: Server, dashboard: Dashboard, db: Database): Container {
    this.logger.info(`Setup root container...`);
    rootContainer.dashboard = dashboard;
    rootContainer.server_editor_socket = server.editorSocket.io;
    rootContainer.server_dashboard_socket = server.dashboardSocket.io;
    rootContainer.db = db;
    return rootContainer;
  }
}

const app = new App();
export default app;
