import { NodeTypeRegistry } from '../../node-type-registry';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class ToggleDurationNode extends Node {
  startTime;
  lastTrigger;
  runState;
  runtimeVal;
  timeoutFunc;

  constructor() {
    super();
  
    this.title = NODE_INFO.hvac["toggle-duration"].title;
    this.description = NODE_INFO.hvac["toggle-duration"].description;
   
    this.addInput('pushButton', Type.BOOLEAN);
    this.addInputWithSettings('runtime', Type.NUMBER, 120, 'Runtime');
    this.addOutput('output', Type.BOOLEAN);
    this.addOutput('remainingRuntime', Type.NUMBER);
    
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: TIME_TYPE.SECONDS, text: 'Seconds' },
          { value: TIME_TYPE.MINUTES, text: 'Minutes' },
          { value: TIME_TYPE.HOURS, text: 'Hours' },
        ],
      },
      value: TIME_TYPE.MINUTES,
    });
    this.settings['useOffDelay'] = NodeSetting.create({
      description: 'Long Hold for OFF?',
      value: false,
      type: SettingType.BOOLEAN,
    });
    this.settings['offDelay'] = NodeSetting.create({
      description: 'OFF Push Delay (Seconds)',
      value: 20,
      type: SettingType.NUMBER,
    });
    this.setSettingsConfig({
      groups: [
        { runtime: { weight: 2 }, time: {} },
      ],
      conditions: {
        offDelay: setting => !!setting['useOffDelay'].value,
      },
    });
  }

  onCreated() {
    this.setOutputData(0, false);
    this.lastTrigger = false;
    this.onAfterSettingsChange();
  }

  onAdded() {
    this.inputs[1]['name'] = `[runtime] (${this.settings['time'].value})`;
    this.EXECUTE_INTERVAL = 10000;
    this.onAfterSettingsChange();
  }

  onExecute() {
    if (this.runState) {
      const remainingMins = (this.runtimeVal - (new Date().valueOf() - this.startTime)) * 1.6667e-5;
      this.setOutputData(1, remainingMins.toFixed(1));
      if (remainingMins <= 0) this.stop();
    }
  }

  onInputUpdated() {
    let trigger = this.getInputData(0);
    if (trigger == null) this.lastTrigger = false;
    if (!trigger) this.properties['offPushStartTime'] = null;
    else if (trigger && !this.lastTrigger) {
      let runtime = this.getInputData(1);
      runtime = TimeUtils.timeConvert(runtime, this.settings['time'].value);
      if (this.runState){ 
        if (this.settings['useOffDelay'].value && this.properties['offPushStartTime'] == null){
          this.startCountdown();
        }
        else this.stop();
      }
      else this.start(runtime);
    }
    this.lastTrigger = trigger;
  }

  startCountdown(){
    this.properties['offPushStartTime'] = Date.now();
    this.timeoutFunc = setInterval(() => {
      const trigger = this.getInputData(0);
      console.log("checking if the button is till held")
      if (!trigger || !this.settings['useOffDelay'].value) {
        this.properties['offPushStartTime'] = null;
        clearInterval(this.timeoutFunc);
      }
      else if ((this.properties['offPushStartTime'] + TimeUtils.timeConvert(this.settings['offDelay'].value, TIME_TYPE.SECONDS)) <= Date.now()) {
        this.properties['offPushStartTime'] = null;
        clearInterval(this.timeoutFunc);
        this.stop();
      }
    }, 1000);
  }

  start(runtime) {
    this.runState = true;
    this.runtimeVal = runtime;
    this.EXECUTE_INTERVAL = Math.max(Math.min(10000, runtime / 10), 250);
    this.startTime = new Date().valueOf();
    this.setOutputData(0, true);
    this.onExecute();
  }

  stop() {
    this.runState = false;
    this.setOutputData(0, false);
    this.setOutputData(1, 0);
  }

  onAfterSettingsChange() {
    this.inputs[1]['name'] = `[runtime] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }
}
NodeTypeRegistry.registerNodeType('hvac/toggle-duration', ToggleDurationNode);
