import { NodeTypeRegistry } from '../../node-type-registry';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import { NODE_INFO } from '../../node-info';

class ActuatorSizingNode extends Node {
  conversionFunction;
  constructor() {
    super();
   
    this.title = NODE_INFO.hvac["actuator-sizing"].title;
    this.description = NODE_INFO.hvac["actuator-sizing"].description;
   
    this.addInput('damperArea(m²)', Type.NUMBER);
    this.addOutput('torque(Nm)', Type.NUMBER);
    this.addOutput('torque(lbf-in)', Type.NUMBER);

    this.settings['units'] = NodeSetting.create({
      description: 'Input Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: true, text: 'Square Meters (m²)' },
          { value: false, text: 'Square Feet (ft²)' },
        ],
      },
      value: true,
    });

    this.settings['type'] = NodeSetting.create({
      description: 'Damper Type',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: 6, text: 'No Seals (Low Resistance)' },
          { value: 7, text: 'With Seals (Medium Resistance)' },
          { value: 8, text: 'Fire/Smoke (High Resistance)' },
        ],
      },
      value: 6,
    });
  }

  onAdded() {
    this.onAfterSettingsChange();
  }

  onInputUpdated() {
    this.onInputOrSettingsChange();
  }

  onAfterSettingsChange() {
    this.updateTitles();
    this.onInputOrSettingsChange();
  }

  private onInputOrSettingsChange() {
    var input = this.getInputData(0);
    if (!Utils.hasInput(input)) {
      this.setOutputData(0, null);
      this.setOutputData(1, null);
      return;
    }

    if (this.settings['units'].value) input = input * 10.7639;
    const type = this.settings['type'].value;

    // calculations are roughly based on https://controltrends.org/by-industry/commercial-hvac/actuators-and-valves/05/sizing-electronic-damper-actuators/ (with sizing reduced slightly);
    const outputm2 = input * type * 0.112985;
    const outputft2 = input * type;

    this.setOutputData(0, outputm2.toFixed(2));
    this.setOutputData(1, outputft2.toFixed(2));
  }

  updateTitles(){
    if (this.settings['units'].value) this.inputs[0].name = 'ductArea(m²)';
    else this.inputs[0].name = 'ductArea(ft²)';
  }
}
NodeTypeRegistry.registerNodeType('hvac/actuator-sizing', ActuatorSizingNode);
