import app from '../app';
import { ContainerRegistry } from '../nodes/container';
import { createNodeDbHelper } from '../nodes/node-db-helper';
import { NodeHookHelper } from '../nodes/node-hook';
import { SerializableHelper } from '../nodes/serializable';
import { PubSubHelpers } from '../pub-sub/PubSubHelpers';
import { UPDATE_NODE_SETTING } from '../shared/websocket-events';
import { IdGenerator } from '../utils/id-generator';
import { server_logger } from '../utils/loggers';
import Namespace = SocketIO.Namespace;

const log = server_logger;

export class DashboardServerSocket {

  io_root: SocketIO.Server;
  io: Namespace;

  constructor(io_root: SocketIO.Server) {
    this.io_root = io_root;
    const io = this.io_root.of('/dashboard');

    this.io = io;
    io.on('connection', function (socket) {
      log.debug('New client connected to dashboard');

      socket.on('disconnect', () => {
        log.debug('Client disconnected from dashboard');
      });

      socket.on('getUiPanel', name => {
        log.debug('getUiPanel: ' + name);
        socket.emit('getUiPanel', app.dashboard.getUiPanel(name));
      });

      socket.on('getUiPanelsList', () => {
        socket.emit('getUiPanelsList', app.dashboard.getUiPanelsList());
      });

      socket.on('nodeMessageToServerSide', n => {
        let cont = ContainerRegistry.instance().get(n.cid);
        if (!cont) {
          log.error(`Can't send node message to server-side. Container [${n.cid}] does not exist`);
          return;
        }

        let node = cont.getSubNode(n.id);
        if (!node) {
          log.error(`Can't send node message to server-side. Node [${IdGenerator.createNodeId(n.cid,
                                                                                              n.id)}] does not exist`);
          return;
        }

        if (!node['onGetMessageToServerSide']) {
          log.error(`Can't send node message to server-side. Node ${node.getReadableId()} ` +
                    `dont have onGetMessageToServerSide method!`);
          return;
        }

        node['onGetMessageToServerSide'](n.message);
      });

      //redirect message
      socket.on('nodeMessageToEditorSide', n => {
        let cont = ContainerRegistry.instance().get(n.cid);
        if (!cont) {
          log.error(`Can't send node message to editor-side. Container [${n.cid}] does not exist`);
          return;
        }

        let node = cont.getSubNode(n.id);
        if (!node) {
          log.error(`Can't send node message to editor-side. Node [${IdGenerator.createNodeId(n.cid,
                                                                                              n.id)}] does not exist`);
          return;
        }

        app.server.editorSocket.io.in(n.cid).emit('nodeMessageToEditorSide', n);
      });

      //redirect message
      socket.on('nodeMessageToDashboardSide', n => {
        let cont = ContainerRegistry.instance().get(n.cid);
        if (!cont) {
          log.error(`Can't send node message to dashboard-side. Container [${n.cid}] does not exist`);
          return;
        }

        let node = cont.getSubNode(n.id);
        if (!node) {
          log.error(`Can't send node message to dashboard-side. Node [${IdGenerator.createNodeId(n.cid,
                                                                                                 n.id)}] does not exist`);
          return;
        }

        app.server.dashboardSocket.io.in(n.cid).emit('nodeMessageToDashboardSide', n);
      });

      socket.on(UPDATE_NODE_SETTING, n => {
        let cont = ContainerRegistry.instance().get(n.cid);
        if (!cont) {
          log.error(`Can't update node settings. Container id [${n.cid}] does not exist`);
          return;
        }

        let node = cont.nodeRegistry().get(n.id);
        if (!node) {
          log.error(`Can't update node settings. Node [${IdGenerator.createNodeId(n.cid, n.id)}] does not exist`);
          return;
        }

        let oldName = node.name;
        let oldIcon = node.icon;
        let oldSettings = SerializableHelper.serialize(node.settings);

        NodeHookHelper.execute(node, 'onBeforeSettingsChange', n.settings, n.name, n.icon);

        for (let key in n.settings) {
          let s = n.settings[key];
          try {
            node.settings[key].value = s.value;
          } catch (error) {
            log.error(error);
          }
        }

        node.name = n.name;
        node.icon = n.icon;
        PubSubHelpers.renameFunction(node);

        Promise.resolve(NodeHookHelper.execute(node, 'onAfterSettingsChange', oldSettings, oldName, oldIcon))
               .then(nn => createNodeDbHelper().updateAsync(ContainerRegistry.instance().rootContainer().db, nn))
               .then(_ => {
                 const args = {
                   id: n.id, cid: n.cid, name: n.name, icon: n.icon, settings: node.settings,
                   oldName: oldName, oldIcon: oldIcon, oldSettings: oldSettings,
                 };
                 app.server.editorSocket.io.emit(UPDATE_NODE_SETTING, args);
                 if (node.isDashboardNode) {
                   app.server.dashboardSocket.io.in(n.cid).emit(UPDATE_NODE_SETTING, args);
                 }
               });
      });

      socket.on('dashboardElementGetNodeState', n => {
        let cont = ContainerRegistry.instance().get(n.cid);
        if (!cont) {
          log.error(`Can't get dashboard element state. Container [${n.cid}] does not exist`);
          return;
        }

        let node = cont.getSubNode(n.id);
        if (!node) {
          log.error(
            `Can't get dashboard element state. Node [${IdGenerator.createNodeId(n.cid, n.id)}] does not exist`);
          return;
        }

        //you can override get state logic
        if (node['onDashboardElementGetNodeState']) {
          node['onDashboardElementGetNodeState'](n.options);
        } else {
          let m = { id: node.id, cid: node.container.id, state: node.properties['state'] };
          socket.emit('dashboardElementGetNodeState', m);
        }
      });

      socket.on('dashboardElementSetNodeState', n => {
        let cont = ContainerRegistry.instance().get(n.cid);
        if (!cont) {
          log.error(`Can't set node state from dashboard element. Container [${n.cid}] does not exist`);
          return;
        }

        let node = cont.getSubNode(n.id);
        if (!node) {
          log.error(`Can't set node state from dashboard element. Node [${IdGenerator.createNodeId(n.cid,
                                                                                                   n.id)}] does not exist`);
          return;
        }

        //you can override get state logic
        if (node['onDashboardElementSetNodeState']) {
          node['onDashboardElementSetNodeState'](n.state);
        } else {
          node.properties['state'] = n.state;
          //send state back to all clients
          let m = { id: node.id, cid: node.container.id, state: node.properties['state'] };
          let panelName = this.settings['ui-panel'].value;
          io.in('' + panelName).emit('dashboardElementGetNodeState', m);
        }
      });

      // client join to dashboard room
      socket.on('join-room', room => {
        if ((<any>socket).room != null) {
          log.debug('Leave dashboard room [' + (<any>socket).room + ']');
          socket.leave((<any>socket).room, () => {
            (<any>socket).room = room;
            socket.join(room, () => {
              log.debug('Join to dashboard room [' + room + ']');
            });
          });
        } else {
          (<any>socket).room = room;
          socket.join(room, () => {
            log.debug('Join to dashboard room [' + room + ']');
          });
        }
      });

      socket.on('leave-room', room => {
        socket.leave(room, () => {
          log.debug('Leave dashboard room [' + room + ']');
        });
        (<any>socket).room = null;
      });
    });
  }
}
