import { Request, Response } from 'express';
import { createNodeLink } from '../../../nodes/node-link';
import { CREATE_LINK, MOVE_LINK, REMOVE_LINK } from '../../../shared/websocket-events';
import { EditorServerSocket } from '../../editor-server-socket';
import { NodeLinkService } from '../../service/NodeLinkService';
import { ApiHandler } from './api-handler';

export class ApiLinkHandler extends ApiHandler {

  private readonly linkService = new NodeLinkService();

  constructor(editorSocket: EditorServerSocket) {
    super(editorSocket);
  }

  onCreate(req: Request, resp: Response) {
    const { source, targets } = req.body;
    this.linkService.create(req.params.cid, source, ...targets)
        .then(result => this.emitSuccess(resp, CREATE_LINK, result, 201))
        .catch(err => this.emitError(resp, err, CREATE_LINK));
  }

  onRemove(req: Request, resp: Response) {
    const { source, targets } = req.body;
    this.linkService.remove(req.params.cid, source, ...targets)
        .then(result => this.emitSuccess(resp, REMOVE_LINK, result))
        .catch(err => this.emitError(resp, err, REMOVE_LINK));
  }

  onMove(req: Request, resp: Response) {
    const cid = req.params.cid;
    const { type, source, targets, another } = req.body;
    const nodeLink = createNodeLink(cid, source, ...targets);
    this.linkService.move(type, nodeLink, another)
        .then(result => this.emitSuccess(resp, MOVE_LINK, result))
        .catch(err => this.emitError(resp, err, MOVE_LINK));
  }
}
