import * as express from 'express';
import { IRouter } from 'express-serve-static-core';
import { ContainerRegistry } from '../../nodes/container';
import { isNull } from '../../utils/helper';
import { Server } from '../server';
import { ApiLinkHandler } from './handler/api-link-handler';
import { ApiNodeHandler } from './handler/api-node-handler';

class ApiEditorRouter {
  private readonly router: IRouter;
  private readonly server: Server;
  private _nodeHandler: ApiNodeHandler;
  private _linkHandler: ApiLinkHandler;

  constructor(server: Server) {
    this.router = express.Router();
    this.server = server;
  }

  setup(loopInterval: number): IRouter {
    this.router.get('/c/:cid', (req, res) => this.nodeHandler().onList(req, res));
    this.router.delete('/c/:cid/n/', (req, res) => this.nodeHandler().onRemove(req, res));
    this.router.post('/c/:cid/n/', (req, res) => this.nodeHandler().onCreate(req, res));
    this.router.post('/c/:cid/move', (req, res) => this.nodeHandler().onPackUp(req, res));
    this.router.post('/c/:cid/export', (req, res) => this.nodeHandler().onExport(req, res));
    this.router.post('/c/:cid/clone', (req, res) => this.nodeHandler().onClone(req, res));
    this.router.post('/c/:cid/import', (req, res) => this.nodeHandler().onImport(req, res));

    this.router.post('/c/:cid/l', (req, res) => this.linkHandler().onCreate(req, res));
    this.router.put('/c/:cid/l', (req, res) => this.linkHandler().onMove(req, res));
    this.router.delete('/c/:cid/l', (req, res) => this.linkHandler().onRemove(req, res));

    setInterval(() => updateActiveNodes(this.server), 500);
    return legacySetup(this.router, this.server, loopInterval);
  }

  nodeHandler() {
    if (isNull(this._nodeHandler)) {
      this._nodeHandler = new ApiNodeHandler(this.server.editorSocket, this.server.dashboardSocket);
    }
    return this._nodeHandler;
  }

  linkHandler() {
    if (isNull(this._linkHandler)) {
      this._linkHandler = new ApiLinkHandler(this.server.editorSocket);
    }
    return this._linkHandler;
  }
}

function updateActiveNodes(server: Server) {
  if (!ContainerRegistry.instance().rootContainer()) return;

  for (let container of ContainerRegistry.instance().list()) {
    let activeNodesIds = [];

    for (let node of container.subNodes()) {
      if (node.isRecentlyActive) {
        node.isRecentlyActive = false;
        activeNodesIds.push(node.id);
      }
    }

    if (activeNodesIds.length > 0) {
      server.editorSocket.io.in('' + container.id).emit('nodes-active', {
        ids: activeNodesIds,
        cid: container.id,
      });
    }
  }
}

function legacySetup(router: IRouter, server: Server, loopInterval: number): IRouter {

  function getDb() {
    return ContainerRegistry.instance().rootContainer().db;
  }

  /**
   * Update node position
   */
  router.put('/c/:cid/n/:id/position', function (req, res) {
    let container = ContainerRegistry.instance().get(req.params.cid);
    if (!container) {
      return res.status(404).send(`Can't update node position. Container id [${req.params.cid}] not found.`);
    }

    let node = container.getSubNode(req.params.id);
    if (!node) {
      return res.status(404).send(
        `Can't update node position. Node id [${req.params.cid}/${req.params.id}] not found.`);
    }

    node.pos = req.body.position;

    if (getDb()) {
      getDb().updateNode(node.id, node.container.id, { $set: { pos: node.pos } });
    }

    server.editorSocket.io.emit('node-update-position', {
      id: req.params.id,
      cid: req.params.cid,
      pos: node.pos,
    });
    res.send(`Node position updated: type [${node.type}] id [${node.container.id}/${node.id}]`);
  });

  /**
   * Update node size
   */
  router.put('/c/:cid/n/:id/size', function (req, res) {
    let container = ContainerRegistry.instance().get(req.params.cid);
    if (!container) return res.status(404).send(`Can't update node size. Container id [${req.params.cid}] not found.`);

    let node = container.getSubNode(req.params.id);
    if (!node) {
      return res.status(404).send(`Can't update node size. Node id [${req.params.cid}/${req.params.id}] not found.`);
    }

    node.size = req.body.size;

    if (getDb()) getDb().updateNode(node.id, node.container.id, { $set: { size: node.size } });

    server.editorSocket.io.emit('node-update-size', {
      id: req.params.id,
      cid: req.params.cid,
      size: node.size,
    });
    res.send(`Node size updated: type [${node.type}] id [${node.container.id}/${node.id}]`);
  });

  /**
   * Update node collapsed
   */
  router.put('/c/:cid/n/:id/collapse', function (req, res) {
    let container = ContainerRegistry.instance().get(req.params.cid);
    if (!container) {
      return res.status(404).send(`Can't update node collapse. Container id [${req.params.cid}] not found.`);
    }

    let node = container.getSubNode(req.params.id);
    if (!node) {
      return res.status(404).send(
        `Can't update node collapse. Node id [${req.params.cid}/${req.params.id}] not found.`);
    }

    node.flags.collapsed = req.body.collapsed;

    if (getDb()) getDb().updateNode(node.id, node.container.id, { $set: { 'flags.collapsed': node.flags?.collapsed } });

    server.editorSocket.io.emit('node-update-collapsed', {
      id: req.params.id,
      cid: req.params.cid,
      'flags.collapsed': node.flags?.collapsed,
    });
    res.send(`Node collapsed updated: type [${node.type}] id [${node.container.id}/${node.id}]`);
  });

//------------------ Container ------------------------
  /**
   * Run step container
   */
  router.get('/state', function (req, res) {
    let state = {
      isRunning: ContainerRegistry.instance().rootContainer().isRunning,
    };
    res.json(state);
  });

  /**
   * Run container
   */
  router.post('/run', function (req, res) {
    ContainerRegistry.instance().rootContainer().run(loopInterval);
    server.editorSocket.io.emit('container-run');
    res.send(`Run container`);
  });

  /**
   * Stop container
   */
  router.post('/stop', function (req, res) {
    ContainerRegistry.instance().rootContainer().stop();
    server.editorSocket.io.emit('container-stop');
    res.send(`Stop container`);
  });

  /**
   * Run step container
   */
  router.post('/step', function (req, res) {
    ContainerRegistry.instance().rootContainer().runStep();
    server.editorSocket.io.emit('container-run-step');
    res.send(`Run step container`);
  });

//------------------ nodes internal requests ------------------------

  router.get('/c/:cid/n/:id*', function (req, res) {
    let cont = ContainerRegistry.instance().get(req.params.cid);
    if (!cont) return res.status(404).send(`Can't send request to node. Container id [${req.params.cid}] not found.`);

    let node = cont.getSubNode(req.params.id);
    if (!node) {
      return res.status(404).send(
        `Can't send request to node. Node id [${req.params.cid}/${req.params.id}] not found.`);
    }

    if (node['onEditorApiGetRequest']) {
      node['onEditorApiGetRequest'](req, res);
    } else {
      return res
        .status(404)
        .send(`Can't send request to node. Node id [${req.params.cid}/${req.params.id}] does not accept requests.`);
    }
  });

  router.post('/c/:cid/n/:id*', function (req, res) {
    let cont = ContainerRegistry.instance().get(req.params.cid);
    if (!cont) return res.status(404).send(`Can't send request to node. Container id [${req.params.cid}] not found.`);

    let node = cont.getSubNode(req.params.id);
    if (!node) {
      return res.status(404).send(
        `Can't send request to node. Node id [${req.params.cid}/${req.params.id}] not found.`);
    }

    if (node['onEditorApiPostRequest']) {
      node['onEditorApiPostRequest'](req, res);
    } else {
      return res
        .status(404)
        .send(`Can't send request to node. Node id [${req.params.cid}/${req.params.id}] does not accept requests.`);
    }
  });

  router.post('/c/:cid/n-type', function (req, res) {
    let cont = ContainerRegistry.instance().get(req.params.cid);
    if (!cont) {
      return res.status(404).send(
        `Can't receive request to node. Container id [${req.params.cid}] not found.`);
    }

    let type = req.body.type;
    if (!type) return res.status(404).send(`Can't receive request to node. Node type is not defined.`);

    let includeSubContainers = req.body.subcontainers == true;
    let nodes = cont.getNodesByType(type, includeSubContainers);
    if (nodes.length == 0) {
      return res
        .status(404)
        .send(`Can't receive request to node. Node type [${type}] not found in container [${req.params.cid}].`);
    }

    nodes.forEach(node => {
      if (node['onEditorApiPostRequest']) {
        node['onEditorApiPostRequest'](req, res);
      } else {
        return res.status(404).send(`Can't receive request to node. Node type [${type}] does not accept requests.`);
      }
    });

    if (!res.headersSent) res.status(400).send(`No node has processed the request.`);
  });

  return router;
}

const apiEditorSetup = (server: Server, loopInterval: number) => new ApiEditorRouter(server).setup(loopInterval);
export { apiEditorSetup };
