import { isNull } from '../../../utils/helper';
import { Node } from '../../node';
import { createNodeDbHelper } from '../../node-db-helper';
import { NODE_INFO } from '../../node-info';
import { Type } from '../../node-io';
import { createLegacyLinkHandler } from '../../node-link';
import { NodeTypeRegistry } from '../../node-type-registry';
import { AbstractFolderIONode } from './abstract-folder-io';

class ContainerOutputNode extends AbstractFolderIONode {

  constructor() {
    super(NODE_INFO.container["folder-output"].title, NODE_INFO.container["folder-output"].description);
    this.addInput('output', null);
  }

  onAdded() {
    super.onAdded();
    if (!this.isUnderContainer() || this.hasItself('outputs')) return;
    const containerNode = this.container.nodeItself();
    const id = containerNode.addOutput(this.name, Type.ANY, undefined, { subNodeId: this.id });
    this.name = containerNode.outputs[id].name;
    createLegacyLinkHandler(containerNode).recomputeOutputLinks(containerNode);
  }

  onRemoved() {
    if (!this.isUnderContainer() || !this.hasItself('outputs')) return;
    let containerNode = this.container.nodeItself();
    let [outputId, _] = this.findParentIOById('outputs');
    containerNode.removeOutput(+outputId);
    createLegacyLinkHandler(containerNode).recomputeOutputLinks(containerNode);
    createNodeDbHelper().update(containerNode, ['size']);
  }

  onAfterSettingsChange() {
    if (!this.isUnderContainer()) return;
    const containerNode = this.container.nodeItself();
    let [outputId, _] = this.findParentIOById('outputs');
    if (isNull(outputId)) {
      return;
    }
    containerNode.outputs[outputId].name = this.name;
    createNodeDbHelper().update(containerNode, ['outputs']);
  }

  onInputUpdated() {
    if (!this.isUnderContainer()) return;
    let [outputId, _] = this.findParentIOById('outputs');
    if (isNull(outputId)) {
      return;
    }
    this.isRecentlyActive = true;
    this.container.nodeItself().setOutputData(+outputId, this.getInputData(0));
  }

  protected correctParentIO(containerNode: Node, slot: number) {
    let output = containerNode.outputs[slot];
    if (isNull(output)) {
      return;
    }
    output.subNodeId = this.id;
    createNodeDbHelper().update(containerNode, ['outputs']);
  }

}

export const FOLDER_OUTPUT = 'container/folder-output';
NodeTypeRegistry.registerNodeType(FOLDER_OUTPUT, ContainerOutputNode, null, true, false, true);
