import { isNull } from '../../../utils/helper';
import { Node } from '../../node';
import { createNodeDbHelper } from '../../node-db-helper';
import { NODE_INFO } from '../../node-info';
import { NodeIO, Type } from '../../node-io';
import { createLegacyLinkHandler } from '../../node-link';
import { NodeTypeRegistry } from '../../node-type-registry';
import { AbstractFolderIONode } from './abstract-folder-io';

class ContainerInputNode extends AbstractFolderIONode {
  constructor() {
    super(NODE_INFO.container["folder-input"].title, NODE_INFO.container["folder-input"].description);
    this.addOutput('input', null);
  }

  onAdded() {
    super.onAdded();
    if (!this.isUnderContainer() || this.hasItself('inputs')) return;
    const containerNode = this.container.nodeItself();
    const id = containerNode.addInput(this.name, Type.ANY, undefined, undefined, { subNodeId: this.id });
    this.name = containerNode.inputs[id].name;
    createLegacyLinkHandler(containerNode).recomputeInputLinks(containerNode);
  }

  onRemoved() {
    if (!this.isUnderContainer() || !this.hasItself('inputs')) return;
    let containerNode = this.container.nodeItself();
    let [inputId, _] = this.findParentIOById('inputs');
    containerNode.removeInput(+inputId);
    createLegacyLinkHandler(containerNode).recomputeInputLinks(containerNode);
    createNodeDbHelper().update(containerNode, ['size']);
  }

  onAfterSettingsChange() {
    if (!this.isUnderContainer()) return;
    const containerNode = this.container.nodeItself();
    let [inputId, _] = this.findParentIOById('inputs');
    if (isNull(inputId)) {
      return;
    }
    containerNode.inputs[inputId].name = this.name;
    createNodeDbHelper().update(containerNode, ['inputs']);
  }

  onExecute() {
    if (!this.isUnderContainer()) return;
    let [_, i] = this.findParentIOById('inputs');
    let input = <NodeIO>i;
    if (input?.updated) this.setOutputData(0, input.data);
  }

  protected correctParentIO(containerNode: Node, slot: number) {
    let input = containerNode.inputs[slot];
    if (isNull(input)) {
      return;
    }
    input.subNodeId = this.id;
    createNodeDbHelper().update(containerNode, ['inputs']);
  }

}

export const FOLDER_INPUT = 'container/folder-input';
NodeTypeRegistry.registerNodeType(FOLDER_INPUT, ContainerInputNode, null, true, false, true);
