import { NodeTypeRegistry } from '../../node-type-registry';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';

class LeadLagSwitchNode extends Node {
  lastRotate;

  constructor() {
    super();
    this.title = NODE_INFO.hvac['lead-lag-switch'].title;
    this.description = NODE_INFO.hvac['lead-lag-switch'].description; 
   
    this.addInput('rotateLead', Type.BOOLEAN);
    this.addInput('unit1', Type.BOOLEAN);
    this.addInput('unit2', Type.BOOLEAN);

    this.addOutput('leadUnit', Type.STRING);
    this.addOutput('enableA', Type.BOOLEAN);
    this.addOutput('enableB', Type.BOOLEAN);

  }

  init(){
    this.properties['currentOrder'] = true;
  }

  onAdded() {
    this.onAfterSettingsChange();
  }

  onAfterSettingsChange() {
    this.onInputUpdated();
  }

  onInputUpdated() {
    const rotate = this.getInputData(0);
    if (rotate && !this.lastRotate) this.properties['currentOrder'] = !this.properties['currentOrder'];
    this.lastRotate = rotate;

    const in1 = this.getInputData(1);
    const in2 = this.getInputData(2);

    if (this.properties['currentOrder']) {
      this.setOutputData(0, 'A'); 
      this.setOutputData(1, !!in1); 
      this.setOutputData(2, !!in2); 
    }
    else {
      this.setOutputData(0, 'B'); 
      this.setOutputData(1, !!in2); 
      this.setOutputData(2, !!in1); 
    }

  }

}
NodeTypeRegistry.registerNodeType('hvac/lead-lag-switch', LeadLagSwitchNode);

