import { default as axios, Method } from 'axios';
import { Container } from '../../../container';
import { Node } from '../../../node';
import { NODE_INFO } from '../../../node-info';
import { NodeSetting, SettingType } from '../../../node-io';

enum HttpMethod {
  GET = 'GET',
  POST = 'POST',
  PUT = 'PUT',
  DELETE = 'DELETE',
  PATCH = 'PATCH',
}

class HttpClientNode extends Node {
  constructor() {
    super();
    this.title = NODE_INFO["protocols/http"]["http-client"].title;
    this.description = NODE_INFO["protocols/http"]["http-client"].description;
    this.addInput('request body');
    this.addOutput('response');
    this.settings['enable'] = NodeSetting.create({ description: 'Enable', value: true, type: SettingType.BOOLEAN });
    this.settings['method'] = NodeSetting.create({
      description: 'Request Method',
      value: HttpMethod.GET,
      type: SettingType.DROPDOWN,
      config: {
        items: Object.keys(HttpMethod),
      },
    });
    this.settings['url'] = NodeSetting.create({ description: 'HTTP URL', value: 'http://', type: SettingType.STRING });
  }

  onInputUpdated() {
    if (!this.isEnabled()) return;
    const methodsWithBody = [HttpMethod.PATCH, HttpMethod.PUT, HttpMethod.POST].map(v =>
      v.toString().toLowerCase(),
    );
    const url = this.getSanitizedUrl();
    const method = this.getSanitizedMethod();
    const input_data = this.inputs[0].data;
    const data =
      methodsWithBody.includes(method) && typeof input_data === 'object' ? input_data : undefined;
    try {
      this.validateMethod(method);
      axios.request({ url, method, data }).then(
        r => this.setOutputData(0, r.data),
        e => this.debugErr(e.message),
      );
    } catch (e) {
      this.debugErr(e.message);
    }
  }

  isEnabled(): boolean {
    return this.settings['enable'].value;
  }

  getSanitizedUrl(): string {
    return this.settings['url'].value.trim();
  }

  getSanitizedMethod(): Method {
    return this.settings['method'].value.trim().toLowerCase();
  }

  validateMethod(method: string) {
    if (Object.keys(HttpMethod).indexOf(method.toUpperCase()) === -1) {
      throw new Error('Unknown HTTP request method: ' + method);
    }
  }

  onCreated() {}
  onAdded() {}
  onAfterSettingsChange() {}
}

Container.registerNodeType('protocols/http-client', HttpClientNode);
