import { NodeTypeRegistry } from '../../node-type-registry';
import { Node } from '../../node';
import { NodeSetting, SettingType, Type } from '../../node-io';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';
import { NODE_INFO } from '../../node-info';

class FlatlineNode extends Node {
  lastValue;
  timeoutFunc;

  constructor() {
    super();
    this.title = NODE_INFO.streams["flatline"].title;
    this.description = NODE_INFO.streams["flatline"].description;
    this.addInput('input', Type.ANY);
    this.addInputWithSettings('interval', Type.NUMBER, 30, 'Interval');
    this.addOutput('output', Type.BOOLEAN);
    this.settings['time'] = NodeSetting.create({
      description: 'Units',
      type: SettingType.DROPDOWN,
      config: {
        items: [
          { value: TIME_TYPE.MILLISECONDS, text: 'Milliseconds' },
          { value: TIME_TYPE.SECONDS, text: 'Seconds' },
          { value: TIME_TYPE.MINUTES, text: 'Minutes' },
          { value: TIME_TYPE.HOURS, text: 'Hours' },
        ],
      },
      value: TIME_TYPE.MINUTES,
    });
    this.setSettingsConfig({
      groups: [{ interval: { weight: 2 }, time: {} }],
    });
  }

  onAdded() {
    this.setOutputData(0, false);
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    this.onInputUpdated();
  }
  
//

  onInputUpdated() {
    const input = this.getInputData(0);
    if (this.inputs[0].updated && input !== this.lastValue) {
      this.lastValue = input;
      this.setOutputData(0, false);
      const interval = TimeUtils.timeConvert(this.getInputData(1), this.settings['time'].value);
      if (this.timeoutFunc) clearTimeout(this.timeoutFunc);
      this.timeoutFunc = setTimeout(() => {
        this.setOutputData(0, true);
      }, interval);
    }
  }

  onAfterSettingsChange() {
    this.inputs[1]['name'] = `[interval] (${this.settings['time'].value})`;
    this.setOutputData(0, false);
    const interval = TimeUtils.timeConvert(this.getInputData(1), this.settings['time'].value);
    if (this.timeoutFunc) clearTimeout(this.timeoutFunc);
    this.timeoutFunc = setTimeout(() => {
      this.setOutputData(0, true);
    }, interval);
  }
}
NodeTypeRegistry.registerNodeType('streams/flatline', FlatlineNode);
