import { RegistryError } from '../utils/errors';
import { isNull } from '../utils/helper';
import { BaseIdGenerator, IdGenerator } from '../utils/id-generator';
import { Container, ContainerRegistry } from './container';
import { Node } from './node';
import { NodeFactory } from './node-factory';
import {
  ContainerRegistryMetadata,
  GlobalNodeRegistryMetadata,
  IContainer,
  IFolderNode,
  INode,
  isFolderNode,
  isNode,
  SerializedNode,
  SubNodeRegistryMetadata,
} from './node-interface';
import { AbstractRegistry, RegistryResult } from './registry';

export class DefaultGlobalNodeRegistry<N extends INode> extends AbstractRegistry<N>
  implements GlobalNodeRegistryMetadata {

  protected constructor() {
    super();
  }

  register(obj: N, force?: boolean, key?: string): N {
    return <N>super.register(obj, force, key);
  }

  get(key: number | string | N): N {
    return super.get(isNode(key) ? this.createKey(<N>key) : key);
  }

  remove(key: number | string | N): N {
    return <N>super.remove(isNode(key) ? this.createKey(<N>key) : key);
  }

  protected createKey(node: N): string | number {
    return IdGenerator.createNodeId(node.cid, node.id);
  }

  protected itemName(): string {
    return 'Global Node';
  }
}

export class GlobalNodeRegistry extends DefaultGlobalNodeRegistry<Node> {
  private static _instance: GlobalNodeRegistry;

  private constructor() {
    super();
  }

  public static instance(): GlobalNodeRegistry {
    if (!GlobalNodeRegistry._instance) {
      GlobalNodeRegistry._instance = new GlobalNodeRegistry();
    }
    return GlobalNodeRegistry._instance;
  }

}

export class NodeRegistryHelper {

  static register(parent: IContainer, nodes: SerializedNode[], containerRegistry: ContainerRegistryMetadata,
                  isCaching?: boolean): Promise<RegistryResult<Node>> {
    return Promise.allSettled(nodes.map(n => NodeFactory.create(n, !isCaching, containerRegistry))
                                   .map(n => NodeRegistryHelper.registerNode(containerRegistry, n, isCaching)))
                  .then(results => results.reduce((r, i) => NodeRegistryHelper.accumulate(r, i),
                                                  new RegistryResult<Node>()));
  }

  static unregister(parent: IContainer, nodes: (number | string | SerializedNode | INode)[],
                    containerRegistry: ContainerRegistryMetadata,
                    isCaching?: boolean): Promise<RegistryResult<Node>> {
    return Promise.allSettled(NodeFactory.standardize(parent, nodes)
                                         .map(n => NodeRegistryHelper.unregisterNode(containerRegistry, n, isCaching)))
                  .then(results => results.reduce((r, i) => NodeRegistryHelper.accumulate(r, i),
                                                  new RegistryResult<Node>()));
  }

  private static registerNode(registry: ContainerRegistryMetadata, node: Node, isCaching: boolean): Promise<Node> {
    const parent = registry.get(node.cid);
    if (isNull(parent)) {
      const nodeId = IdGenerator.createNodeId(node.cid, node.id);
      return Promise.reject(new RegistryError(`Not found parent container of node '${nodeId}'`, node));
    }
    if (isFolderNode(node)) {
      registry.register((<IFolderNode><unknown>node).sub_container, true);
    }
    return <Promise<Node>>parent.nodeRegistry().register(node, true, null, isCaching);
  }

  private static unregisterNode(registry: ContainerRegistryMetadata, node: INode, isCaching: boolean): Promise<Node> {
    const parent = registry.get(node.cid);
    if (isNull(parent)) {
      const nodeId = IdGenerator.createNodeId(node.cid, node.id);
      return Promise.reject(new RegistryError(`Not found parent container of node '${nodeId}'`, node));
    }
    return <Promise<Node>>parent.nodeRegistry().remove(node, isCaching);
  }

  private static accumulate(result: RegistryResult<Node>,
                            item: PromiseFulfilledResult<Node> | PromiseRejectedResult) {
    if (item.status === 'rejected') {
      if (item.reason instanceof RegistryError) {
        result.errors.push(item.reason);
      } else {
        const err = new RegistryError<any>(item.reason instanceof Error ? item.reason.message : item.reason);
        err.stack = item.reason instanceof Error ? item.reason.stack : null;
        result.errors.push(err);
      }
      result.errors.push(item.reason instanceof RegistryError ? item.reason : new RegistryError(item.reason));
    } else {
      result.objects.push(item.value);
    }
    return result;
  }

}

export class DefaultContainerRegistry<T extends IContainer> extends AbstractRegistry<T>
  implements ContainerRegistryMetadata {

  readonly idGenerator: BaseIdGenerator;

  protected constructor(idGenerator: BaseIdGenerator) {
    super();
    this.idGenerator = idGenerator;
  }

  register(container: T, force?: boolean): T {
    const c = this.optimizeBeforeRegister(this.validate(container, force, null), force);
    return <T>this.doRegister(this.createKey(c), c, force);
  }

  remove(id: number | string): T {
    return <T>super.remove(id);
  }

  clear() {
    super.clear();
  }

  rootContainer(): IContainer {
    return this.get(IdGenerator.ROOT);
  }

  protected itemName(): string {
    return 'Container';
  }

  protected optimizeBeforeRegister(container: T, force: boolean): T {
    if ((this.rootContainer() && this.idGenerator.shouldGenerateCid(container.id)) ||
        (this.has(container.id) && !force)) {
      container.id = this.idGenerator.genContainerId(container.id);
    }
    if (this.rootContainer()) {
      container.side = this.rootContainer().side;
    }
    return super.optimizeBeforeRegister(container, force);
  }

  protected createKey(container: T): string | number {
    return container.id;
  }
}

export class DefaultSubNodeRegistry<T extends INode> extends AbstractRegistry<T> implements SubNodeRegistryMetadata {

  readonly globalNodeRegistry: GlobalNodeRegistryMetadata;
  readonly containerRegistry: ContainerRegistryMetadata;
  readonly idGenerator: BaseIdGenerator;
  protected readonly _parent: IContainer;

  constructor(parent: IContainer, containerRegistry?: ContainerRegistryMetadata,
              globalNodeRegistry?: GlobalNodeRegistryMetadata) {
    super();
    this._parent = parent;
    this.globalNodeRegistry = globalNodeRegistry ?? GlobalNodeRegistry.instance();
    this.containerRegistry = containerRegistry ?? ContainerRegistry.instance();
    this.idGenerator = this.containerRegistry.idGenerator;
  }

  parent(): IContainer {
    return this._parent;
  }

  register(node: T, force?: boolean, key?: string, isCaching?: boolean): Promise<T> {
    return Promise.resolve(this.optimizeBeforeRegister(this.validate(node, force, key), force))
                  .then(node => this.doRegister(this.createKey(node), node, force, isCaching));
  }

  remove(node: T | number | string, isCaching?: boolean): Promise<T> {
    let n = this.globalNodeRegistry.remove(isNode(node) ? <INode>node : <string>node);
    if (isFolderNode(n)) {
      this.containerRegistry.remove((<IFolderNode>n).sub_container.id);
    }
    return <Promise<T>>Promise.resolve(super.remove(n.id));
  }

  get(id: number | string | T): T {
    return super.get((<T>id)?.id ?? id);
  }

  protected itemName(): string {
    return `Node in Container '${this._parent.id}'`;
  }

  protected optimizeBeforeRegister(node: T, force: boolean): T {
    if (this.has(node.id) && !force || this.idGenerator.shouldGenerateNid(node.cid, node.id)) {
      node.id = this.idGenerator.genNodeId(node.cid, node.id);
    }
    node.container = this.parent();
    node.cid = this.parent().id;
    node.side = this.parent().side;
    node.title = node.title ?? node.type;
    if (isFolderNode(node)) {
      const cNode = <IFolderNode><unknown>node;
      cNode.sub_container = this.containerRegistry.register(cNode.sub_container, force);
    }
    return super.optimizeBeforeRegister(node, force);
  }

  protected doRegister(k: string | number, node: T, force: boolean = false, isCaching?: boolean): Promise<T> {
    this.globalNodeRegistry.register(<INode>node, force);
    return Promise.resolve(super.doRegister(k, node, force));
  }

  protected createKey(node: T): string | number {
    return node.id;
  }
}
