import { v4 } from 'slugid';
import { isNotNull, isNumeric } from './helper';

export abstract class BaseIdGenerator {
  static readonly UNINITIALIZED = -1;
  static readonly ROOT = 0;

  static createNodeId(cId: number | string, nId: any) {
    if (isNotNull(cId)) {
      if (isNumeric(nId)) {
        return `c${cId}n${nId}`;
      } else {
        return nId;
      }
    }
    return null;
  }

  abstract genNodeId(cid?: string | number, id?: string | number);

  abstract genContainerId(cid?: string | number);

  shouldGenerateCid(id: string | number): boolean {
    return id === BaseIdGenerator.ROOT || id === BaseIdGenerator.UNINITIALIZED;
  }

  shouldGenerateNid(cid: string | number, id: string | number): boolean {
    return this.shouldGenerateCid(id);
  }
}

export class NoChangeId extends BaseIdGenerator {

  genContainerId(cid?: string | number) {
    return cid ?? BaseIdGenerator.UNINITIALIZED;
  }

  genNodeId(cid?: string | number, id?: string | number) {
    return id ?? BaseIdGenerator.UNINITIALIZED;
  }

}

/**
 * ID generator
 */
export class IdGenerator extends BaseIdGenerator {

  private static _instance: IdGenerator;

  private constructor() {
    super();
  }

  public static instance(): IdGenerator {
    if (!IdGenerator._instance) {
      IdGenerator._instance = new IdGenerator();
    }

    return IdGenerator._instance;
  }

  genContainerId(cid?: string | number) {
    return v4();
  }

  genNodeId(cid?: string | number, id?: string | number) {
    return v4();
  }

}
