import { NodeTypeRegistry } from '../../node-type-registry';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import Utils from '../../utils';
import TimeUtils, { TIME_TYPE } from '../../utils/time-utils';
import { SSL_OP_SSLEAY_080_CLIENT_DH_BUG } from 'constants';

class LeadLagStagingNode extends Node {
  //Inputs
  ENABLESLOT = 0;
  ROTATESLOT = 1;
  UPSLOT = 2;
  DOWNSLOT = 3;
  DELAYSLOT = 4;
  MINONSLOT = 5;
  FAULTSTARTSLOT = 6;

  //Outputs
  JSONSLOT = 0;
  ORDERSLOT = 1;
  STAGESLOT = 2;
  OUTPUTSTARTSLOT = 3;

  lastRotate;
  lastEnable;

  constructor() {
    super();
    this.title = NODE_INFO.hvac['lead-lag-staging'].title;
    this.description = NODE_INFO.hvac['lead-lag-staging'].description; 
   
    this.addInputWithSettings('enable', Type.BOOLEAN, true, 'Enable');
    this.addInput('rotateLead', Type.BOOLEAN);
    this.addInput('stageUp', Type.BOOLEAN);
    this.addInput('stageDown', Type.BOOLEAN);
    this.settings['numUnits'] = NodeSetting.create({
      description: 'Number of Units',
      value: 2,
      type: SettingType.NUMBER,
    });
    this.addInputWithSettings('stageUpDelay (minutes)', Type.NUMBER, 10, 'Stage Up Delay (Minutes)');
    this.addInputWithSettings('minOnTime (minutes)', Type.NUMBER, 5, 'Minimum On Time (Minutes)');
    this.settings['stageUpOnFault'] = NodeSetting.create({
      description: 'Stage Up On Running Unit Fault?',
      value: false,
      type: SettingType.BOOLEAN,
    });

    this.addOutput('outputJSON', Type.JSON);
    this.addOutput('order', Type.STRING);
    this.addOutput('currentStage', Type.NUMBER);

    this.setSettingsConfig({
      groups: [],
      conditions: {},
    });
  }

  init(){
    this.properties['currentOrder'] = [1,2];
    this.properties['orderIndex'] = 0;
    this.properties['rotationsList'];
  }

  onCreated() {
    this.onAfterSettingsChange();
  }

  onAdded() {
    this.EXECUTE_INTERVAL = 10000;
    this.onAfterSettingsChange();
  }

  onExecute(){
    this.onInputUpdated();
  }

  onAfterSettingsChange() {
    this.updateCount();
    this.onInputUpdated();
  }

  onInputUpdated() {
    this.initializeOutputJSON();
    const enable = this.getInputData(this.ENABLESLOT);

    const rotate = this.getInputData(this.ROTATESLOT);
    if (rotate && !this.lastRotate){
      this.properties['orderIndex']++;
      if (this.properties['orderIndex'] >= this.properties['rotationsList'].length) this.properties['orderIndex'] = 0;
      this.properties['lastRotateTime'] = Date.now();
    }
    this.lastRotate = rotate;
    this.properties['currentOrder'] = this.properties['rotationsList'][this.properties['orderIndex']];

    if (enable) {
      if (!this.lastEnable) {
        this.properties['currentStage'] = 1;
        this.properties['stageStartTime'] = Date.now();
      }

      const stageUP = this.getInputData(this.UPSLOT);
      const stageDOWN = this.getInputData(this.DOWNSLOT);
      const upDelay = TimeUtils.timeConvert(this.getInputData(this.DELAYSLOT), TIME_TYPE.MINUTES);
      const minOnDelay = TimeUtils.timeConvert(this.getInputData(this.MINONSLOT), TIME_TYPE.MINUTES);
      const currentStage = this.properties['currentStage'];
      var stageChange = 0;


      if(stageUP && !stageDOWN && currentStage < this.properties['numEnableOutputs']){
        if (Date.now() > this.properties['stageStartTime'] + upDelay) {
          stageChange++;
        }
      }

      if(stageDOWN && !stageUP && currentStage > 1){
        if (Date.now() > this.properties['stageStartTime'] + minOnDelay) {
          stageChange--;
        }
      }

      if (this.settings['stageUpOnFault'].value){
        var faultArray = [];
        var fault, index;
        for (var i = 0; i < this.settings['numUnits'].value; i++) {
          fault = this.getInputData(this.FAULTSTARTSLOT + i);
          index = this.properties['currentOrder'].indexOf(this.convertNumToLetter(i))
          fault ? faultArray[index] = true : faultArray[index] = false;
        }
        this.properties['outputJSON']['faultArray'] = faultArray;
        while ((currentStage + stageChange) < this.properties['numEnableOutputs'] && faultArray[currentStage + stageChange - 1]){
          stageChange++
        }
      }
 
      if (stageChange !== 0){
        if (this.properties['currentStage'] + stageChange > this.properties['numEnableOutputs']) this.properties['currentStage'] = this.properties['numEnableOutputs'];
        else this.properties['currentStage'] += stageChange;
        this.properties['stageStartTime'] = Date.now();
      }

      this.setOutputs();
    }
    // For disabled controller
    else this.resetController();

    this.lastEnable = enable;
    this.setOutputJSON();

  }

  resetController(){
    this.properties['currentStage'] = 0;
    this.properties['stageStartTime'] = null;
    this.setOutputData(this.ORDERSLOT, this.properties['currentOrder'].toString());
    this.setOutputData(this.STAGESLOT, 0);
    for(var x = 0; x < this.properties['numEnableOutputs']; x++){
      this.setOutputData(x + this.OUTPUTSTARTSLOT, false);
    }
  }

  setOutputs() {
    this.setOutputData(this.ORDERSLOT, this.properties['currentOrder'].toString());
    this.setOutputData(this.STAGESLOT, this.properties['currentStage']);
    var output, index;
    for(var x = 0; x < this.properties['numEnableOutputs']; x++){
      index = this.properties['currentOrder'].indexOf(this.convertNumToLetter(x));
      output = this.properties['currentStage'] >= (index+1);
      this.setOutputData(x + this.OUTPUTSTARTSLOT, output);
    }
  }

  updateCount(){
    if (this.properties['numEnableOutputs'] !== this.settings['numUnits'].value || this.properties['numFaultInputs'] !== this.settings['numUnits'].value){
      this.properties['orderIndex'] = 0;
    }
    this.updateOutputsCount();
    this.updateInputsCount();
    this.generateRotations();
    this.properties['currentOrder'] = this.properties['rotationsList'][this.properties['orderIndex']];
  }

  generateRotations(){
    var list = [];
    const count = this.properties['numEnableOutputs'];
    for (var x = 0; x < count; x++) list.push(this.convertNumToLetter(x));
    var result = [];
    result.push(Array.from(list));
    var shiftVal;
    for (var y = 1; y < count; y++) {
      shiftVal = list.shift();
      list.push(shiftVal);
      result.push(Array.from(list));
    }
    this.properties['rotationsList'] = result;
  }

  updateOutputsCount() {
    this.settings['numUnits'].value = Math.min(Math.max(2, this.settings['numUnits'].value), 10);
    this.properties['numEnableOutputs'] = this.getOutputsCount() - this.OUTPUTSTARTSLOT;
    const diff = this.settings['numUnits'].value - this.properties['numEnableOutputs'];
    if (diff == 0) return;
    //add
    let num = null;
    if (diff > 0) for (let i = 0; i < diff; i++) {
      num = this.addOutput('Unit'+ this.convertNumToLetter(this.properties['numEnableOutputs'] + i), Type.BOOLEAN);
      this.setOutputData(num, false);
    }
    //remove
    if (diff < 0) {
      let ids = Object.keys(this.outputs);
      for (let i = 0; i > diff; i--) {
        let id = ids[ids.length + i - 1];
        this.removeOutput(+id);
      }
    }
    this.properties['numEnableOutputs'] = this.settings['numUnits'].value;
    if (this.properties['currentStage'] > this.properties['numUnits']) this.properties['currentStage'] = this.properties['numUnits'];
  }

  updateInputsCount() {
    this.settings['numUnits'].value = Math.min(Math.max(2, this.settings['numUnits'].value), 10);
    this.properties['numFaultInputs'] = this.getInputsCount() - this.FAULTSTARTSLOT;
    var requiredFaultInputs = 0;
    if (this.settings['stageUpOnFault'].value) requiredFaultInputs = this.settings['numUnits'].value;
    const diff = requiredFaultInputs - this.properties['numFaultInputs'];
    if (diff == 0) return;
    //add
    let num = null;
    if (diff > 0) for (let i = 0; i < diff; i++) {
      num = this.addInput('Fault'+ this.convertNumToLetter(this.properties['numFaultInputs'] + i), Type.BOOLEAN);
    }
    //remove
    if (diff < 0) {
      let ids = Object.keys(this.inputs);
      for (let i = 0; i > diff; i--) {
        let id = ids[ids.length + i - 1];
        this.removeInput(+id);
      }
    }
    if (this.settings['stageUpOnFault'].value) this.properties['numFaultInputs'] = this.settings['numUnits'].value;
    else this.properties['numFaultInputs'] = 0;
  }

  convertNumToLetter(convertNumber:number = 0){
    return String.fromCharCode(65 + convertNumber);
  }

  initializeOutputJSON(){
    this.properties['outputJSON'] = {
      enableInput: this.getInputData(this.ENABLESLOT),
      rotateInput: this.getInputData(this.ROTATESLOT),
      stageUpInput: this.getInputData(this.UPSLOT),
      stageDownInput: this.getInputData(this.DOWNSLOT),
      stageUpDelay: this.getInputData(this.DELAYSLOT),
      minOnTime: this.getInputData(this.MINONSLOT),
      order: null,
      orderIndex: null,
      numUnits: null,
      currentStage: null,
      stageArray: [],
      useFaultInputs: null,
      faultArray: [],
      currentTime: null,
      stageStartTime: null,
    };
  }

  setOutputJSON(){
    this.properties['outputJSON']['order'] = this.properties['currentOrder'];
    this.properties['outputJSON']['orderIndex'] = this.properties['orderIndex'];
    this.properties['outputJSON']['numUnits'] = this.settings['numUnits'].value;
    this.properties['outputJSON']['currentStage'] = this.properties['currentStage'];
    this.properties['outputJSON']['useFaultInputs'] = this.settings['stageUpOnFault'].value;
    for(var x = 0; x < this.properties['numEnableOutputs']; x++){
      this.properties['outputJSON']['stageArray'].push(this.outputs[String(x+this.OUTPUTSTARTSLOT)].data);
    }
    this.properties['outputJSON']['stageStartTime'] = this.properties['stageStartTime'];
    this.properties['outputJSON']['currentTime'] = Date.now();
    this.setOutputData(this.JSONSLOT, this.properties['outputJSON']);
  }

}
NodeTypeRegistry.registerNodeType('hvac/lead-lag-staging', LeadLagStagingNode);

