import { Logger } from 'logplease';
import { Database } from '../../database/database';
import { ContainerRegistry } from '../../nodes/container';
import { createNodeDbHelper, NodeDbHelper } from '../../nodes/node-db-helper';
import {
  ContainerRegistryMetadata,
  isFolderNode,
  SerializedFolderNode,
  SerializedNode,
} from '../../nodes/node-interface';
import { BaseIdGenerator } from '../../utils/id-generator';
import { node_service_logger } from '../../utils/loggers';

export abstract class NodeService {

  protected readonly cRegistry: ContainerRegistryMetadata;
  protected readonly dbHelper: NodeDbHelper;
  protected readonly idGenerator: BaseIdGenerator;
  protected readonly logger: Logger = node_service_logger;

  constructor(cRegistry?: ContainerRegistryMetadata, dbHelper?: NodeDbHelper, idGenerator?: BaseIdGenerator) {
    this.cRegistry = cRegistry ?? ContainerRegistry.instance();
    this.idGenerator = idGenerator ?? this.cRegistry.idGenerator;
    this.dbHelper = dbHelper ?? createNodeDbHelper();
  }

  protected getNodesInContainer(database: Database, nodes: SerializedNode[]): Promise<SerializedNode[]> {
    const list = nodes.filter(n => isFolderNode(n))
                      .map(n => <SerializedFolderNode>n)
                      .map(n => this.dbHelper.listByContainer(database, n.sub_container.id)
                                    .then(nodes => this.getNodesInContainer(database, nodes)));
    return Promise.all([Promise.resolve(nodes), ...list]).then(r => r.flatMap(nodes => nodes));
  }
}
