/**
 * Represents for ETL service (Extract - Transform - Load data, a.k.a import/export)
 */
export class NodeETLService extends NodeService {

  /**
   * Pack up nodes to new container under specific one
   * @param {number} parentId current parent container id
   * @param {number[]} ids node ids to pack up
   * @param atPos At position
   * @param {ContainerRegistryMetadata} metadata container registry
   * @return {Promise<PackResult>} pack result
   */
  packUpNodes(parentId: string | number, ids: (string | number)[], atPos: [number, number],
              metadata?: ContainerRegistryMetadata): Promise<PackResult> {
    this.logger.info(`Packing nodes [${ids}] in container '${parentId}' into new container...`);
    const reg: ContainerRegistryMetadata = metadata ?? this.cRegistry;
    const v = ContainerRegistryValidator.mustExisted(parentId, reg);
    return v.then(p => ListValidator.notEmpty(ids, `No node to pack-up`)
                                    .then(nodeIds => this.doExport(parentId, nodeIds, reg))
                                    .then(nodes => this.doPackUp(p, atPos, nodes, reg)));
  }

  /**
   * Clone nodes to other nodes under specific container
   * @param {number} parentId current parent container id
   * @param {number[]} ids node ids to clone
   * @param {[number, number]} atPos to move
   * @param {ContainerRegistryMetadata} metadata container registry
   * @return {Promise<CloneResult>} clone result
   */
  cloneNodes(parentId: string | number, ids: number[], atPos: [number, number],
             metadata?: ContainerRegistryMetadata): Promise<CloneResult> {
    this.logger.info(`Cloning nodes [${ids}] inside container id '${parentId}'...`);
    return ListValidator.notEmpty(ids, `No node to clone`)
                        .then(nodeIds => this.doExport(parentId, nodeIds, metadata))
                        .then(nodes => this.doImport(nodes, parentId, metadata, atPos))
                        .then(r => initETLResult<CloneResult>(CloneResult, parentId, ids.length, r));
  }

  /**
   * Export nodes and children nodes from specific container
   * @param {number} parentId
   * @param {number[]} ids
   * @param {ContainerRegistryMetadata} metadata
   * @return {Promise<SerializedNode[]>}
   */
  exportNodes(parentId: string | number, ids: number[], metadata?: ContainerRegistryMetadata): Promise<ExportResult> {
    return ListValidator.notEmpty(ids, `No node to export`)
                        .then(nodeIds => this.doExport(parentId, nodeIds, metadata))
                        .then(l => ListValidator.notEmpty<SerializedNode>(l, `Not found any [${ids}] to export`))
                        .then(nodes => new ExportResult(parentId, nodes));
  }

  /**
   * Import nodes into specific container
   * @param {number} parentId
   * @param {SerializedNode[]} nodes
   * @param {[number, number]} atPos
   * @param {ContainerRegistryMetadata} metadata?
   * @return {Promise<ImportResult>}
   */
  importNodes(parentId: string | number, nodes: SerializedNode[], atPos: [number, number],
              metadata?: ContainerRegistryMetadata): Promise<ImportResult> {
    return ListValidator.notEmpty(nodes, `No nodes to import`)
                        .then(n => this.doImport(n, parentId, metadata, atPos))
                        .then(r => initETLResult<ImportResult>(ImportResult, parentId, nodes.length, r));
  }

  private doExport(cid: string | number, ids: (string | number)[],
                   metadata: ContainerRegistryMetadata): Promise<SerializedNode[]> {
    this.logger.info(`Exporting [${ids}] nodes in container '${cid}'...`);
    const registry: ContainerRegistryMetadata = metadata ?? this.cRegistry;
    const database = registry.rootContainer()['db'];
    return ContainerRegistryValidator.mustExisted(cid, registry)
                                     .then(c => unionToArray(ids.map(nid => IdGenerator.createNodeId(c.id, nid))))
                                     .then(exportIds => this.dbHelper.listByIds(database, exportIds))
                                     .then(nodes => this.getNodesInContainer(database, nodes));
  }

  private createFolder(parent: IContainer, registry: ContainerRegistryMetadata,
                       atPos: [number, number]): IFolderNode {
    this.logger.info(`Creating new folder node into container id '${parent.id}'...`);
    const id = this.idGenerator.genNodeId(parent.id, IdGenerator.UNINITIALIZED);
    const folder = <IFolderNode><unknown>NodeFactory.create(<SerializedFolderNode>{
      id, cid: parent.id, type: CONTAINER_NODE_TYPE, pos: atPos, sub_container: { id },
    }, true, registry);
    folder.container = parent;
    folder.sub_container = registry.register(folder.sub_container);
    this.logger.info(`Cached new folder node id '${IdGenerator.createNodeId(parent.id, folder.id)}' ` +
                     `with container id '${folder.sub_container.id}'`);
    return folder;
  }

  private doPackUp(parent: IContainer, atPos: [number, number], nodes: SerializedNode[],
                   registry: ContainerRegistryMetadata): Promise<PackResult> {
    const folder = this.createFolder(parent, registry, atPos);
    const cid = folder.sub_container.id;
    const ids = nodes.map(n => n.id);
    this.logger.info(`Analyzing ${ids.length} nodes to move from container '${parent.id}' ` +
                     `into container id '${cid}'...`);
    return Promise.resolve(new NodeTree(cid, this.idGenerator, parent.id))
                  .then(tree => tree.compute(nodes))
                  .then(tree => this.dbHelper.batchInsert(registry.rootContainer()['db'],
                                                          [SerializableHelper.serialize(folder), ...tree.flatten()]))
                  .then(pNodes => new NodeCRUDService().removeNodes(parent, ids).then(rr => ({ nodes: pNodes, rr })))
                  .then(({ nodes, rr }) => NodeRegistryHelper.register(parent, nodes, registry, true)
                                                             .then(pr => ({ pr, rr })))
                  .then(({ pr, rr }) => initETLResult<PackResult>(PackResult, parent.id, ids.length, pr, { cid, rr }));
  }

  private doImport(nodes: SerializedNode[], parentId: string | number, metadata: ContainerRegistryMetadata,
                   atPos: [number, number]): Promise<RegistryResult<Node>> {
    this.logger.info(`Analyzing ${nodes.length} nodes to import into container id '${parentId}'...`);
    const registry: ContainerRegistryMetadata = metadata ?? this.cRegistry;
    const db = registry.rootContainer()['db'];
    return ContainerRegistryValidator.mustExisted(parentId, registry)
                                     .then(c => Promise.resolve(new NodeTree(c.id, this.idGenerator))
                                                       .then(tree => tree.compute(nodes, atPos))
                                                       .then(t => this.dbHelper.batchInsert(db, t.flatten()))
                                                       .then(n => NodeRegistryHelper.register(c, n, registry, true)));
  }

}

export class NodeTree {
  private readonly destCId: string;
  private readonly contextCId: string;
  private readonly idGenerator: BaseIdGenerator;
  private readonly logger = node_service_logger;
  private tree: NodeBranch;
  private outputs: SerializedNode[];

  constructor(destCId: string | number, idGenerator: BaseIdGenerator, contextCId?: string | number) {
    this.destCId = destCId + '';
    this.contextCId = isNull(contextCId) ? this.destCId : contextCId + '';
    this.idGenerator = idGenerator;
  }

  /**
   * Build and normalize node tree
   * @param {SerializedNode[]} nodes the given nodes
   * @param {[number, number]} atPos position
   * @return {NodeTree}
   */
  compute(nodes: SerializedNode[], atPos?: [number, number]): NodeTree {
    const { containers, nodeMap } = this.analyze(nodes);
    if (isEmpty(nodeMap)) {
      return this;
    }
    this.tree = this.build(containers, nodeMap);
    this.tree = this.tree.normalize(atPos);
    this.outputs = this.tree.flatten();
    return this;
  }

  /**
   * Returns flatten nodes to insert into database
   * @return {SerializedNode[]} list serialized node
   */
  flatten(): SerializedNode[] {
    return this.outputs ?? [];
  }

  private analyze(nodes: SerializedNode[]): { containers: { [cid: string]: SerializedContainer }, nodeMap: { [cid: string]: SerializedNode[] } } {
    return nodes.reduce(
      (o, n) => {
        if (isFolderNode(n)) {
          const cid = (<SerializedFolderNode>n).sub_container.id;
          o.containers[cid] = { id: this.idGenerator.genContainerId(cid) };
        }
        o.nodeMap[n.cid] = isNull(o.nodeMap[n.cid]) ? [n] : [...o.nodeMap[n.cid], n];
        return o;
      }, { containers: {}, nodeMap: {} });
  }

  private build(containers: { [cid: string]: SerializedContainer },
                nodeMap: { [cid: string]: SerializedNode[] }): NodeBranch {
    const remain = Object.keys(nodeMap).filter(cid => isNull(containers[cid]));
    if (remain.length > 1) {
      throw new Error(`The given nodes has invalid node(s) that is under non-existed container(s) ` +
                      `[${remain.filter(id => id !== this.destCId)}]`);
    }
    const mapContainers = { ...containers, ...objectOf(this.contextCId, { id: this.destCId }) };
    this.logger.info(`Building node tree with ${Object.keys(containers).length} new containers ` +
                     `into container '${this.destCId}'...`);
    if (remain.length === 1 && remain[0] !== this.contextCId) {
      nodeMap[this.contextCId] = nodeMap[remain[0]];
      delete nodeMap[remain[0]];
    }
    const remapNodes = arrayToObject(Object.keys(mapContainers), cid => cid, cid => nodeMap[cid] ?? []);
    return this.buildBranch(this.contextCId, mapContainers, remapNodes);
  }

  private buildBranch(parent: string, containers: { [p: string]: SerializedContainer },
                      nodeMap: { [p: string]: SerializedNode[] }): NodeBranch {
    this.logger.info(`Analyzing ${nodeMap[parent].length} nodes(s) in container '${containers[parent].id}'...`);
    return new NodeBranch(containers[parent]).add(nodeMap[parent], id => this.idGenerator.genNodeId(parent, id),
                                                  cid => this.buildBranch(cid, containers, nodeMap));

  }

}

class NodeBranch {

  readonly parent: SerializedContainer;
  readonly siblingIds: { [nId: string]: string } = {};
  private readonly nodes: { [nId: string]: { node: SerializedNode, descendant?: NodeBranch } } = {};
  private readonly nodeLinks: NodeLink[] = [];
  private readonly logger = node_service_logger;
  private outputs: SerializedNode[];

  constructor(parent: SerializedContainer) {
    this.parent = parent;
  }

  add(nodes: SerializedNode[], generateId: (id) => string, lookup: (cid) => NodeBranch): NodeBranch {
    nodes.forEach(node => {
      const descendant = isFolderNode(node) ? lookup((<IFolderNode>node).sub_container.id) : null;
      this.siblingIds[node.id] = generateId(node.id);
      this.nodes[node.id] = { node, descendant };
      let slot = 0;
      for (const [k, input] of Object.entries(node.inputs ?? {})) {
        if (isNull(input.link)) {
          continue;
        }
        this.nodeLinks.push(createNodeLink(this.parent.id, Link.create(input.link),
                                           Link.create({ nodeId: node.id, ioId: k, slot: ++slot })));
        input.link = null;
      }
      slot = 0;
      for (const [k, output] of Object.entries(node.outputs ?? {})) {
        if (isEmpty(output.links)) {
          continue;
        }
        this.nodeLinks.push(createNodeLink(this.parent.id, Link.create({ nodeId: node.id, ioId: k, slot: ++slot }),
                                           ...output.links.map(l => Link.create(l))));
        output.links = [];
      }
    });
    return this;
  }

  normalize(atPos?: [number, number]): NodeBranch {
    const relativePos = this.computeRelativePositions(atPos);
    this.logger.info(`Optimizing ${this.nodeLinks.length} node link(s) in container '${this.parent.id}'...`);
    const handler = createLinkHandler(null, new ContainerValidator(this.parent), new NodeValidator(this.nodes), true);
    const lr = handler.batchCreate(...this.nodeLinks);
    this.logger.info(`Normalizing ${Object.keys(this.nodes).length} nodes in container '${this.parent.id}'...`);
    this.outputs = Object.keys(this.nodes)
                         .map(nId => this.nodes[nId] = this.normalizeNode(this.nodes[nId], lr, relativePos))
                         .flatMap(branch => this.flattenBranch(branch));
    return this;
  }

  flatten(): SerializedNode[] {
    return this.outputs ?? [];
  }

  private computeRelativePositions(atPos: [number, number]): [number, number] {
    const allPos = Object.values(this.nodes).map(b => b.node.pos);
    return isNull(atPos) ? null : Shape2D.relative(Shape2D.avgPos(allPos), atPos);
  }

  private flattenBranch(branch: { node: SerializedNode, descendant?: NodeBranch }): SerializedNode[] {
    if (isNull(branch.descendant)) {
      return [branch.node];
    }
    const nodes = Object.values(branch.descendant.nodes).flatMap(sub => this.flattenBranch(sub));
    return [branch.node, ...nodes];
  }

  private normalizeNode(branch: { node: SerializedNode; descendant?: NodeBranch }, linkResult: LinkResult,
                        relativePos?: [number, number]): { node: SerializedNode; descendant?: NodeBranch } {
    if (isFolderNode(branch.node)) {
      const itself = <SerializedFolderNode>branch.node;
      itself.sub_container.id = branch.descendant.parent.id;
      branch.descendant = branch.descendant.normalize();
    }
    const oldId = branch.node.id + '';
    branch.node.id = this.siblingIds[branch.node.id];
    branch.node.cid = this.parent.id;
    branch.node.type = NodeTypeRegistry.instance().lookupNodeType(branch.node.type);
    branch.node.pos = Shape2D.move(relativePos, branch.node.pos);
    branch.node.inputs = this.normalizeNodeInputs(oldId, branch.node.inputs, linkResult, branch.descendant);
    branch.node.outputs = this.normalizeNodeOutputs(oldId, branch.node.outputs, linkResult, branch.descendant);
    return branch;
  }

  private normalizeNodeInputs(nid: string, inputs: { [p: number]: SerializedNodeInput }, linkResult: LinkResult,
                              descendant?: NodeBranch) {
    return mapToObjectSameKey(inputs, (k, input) => {
      const link = linkResult.getLinkByInput(this.parent.id, nid, k);
      input.link = isNull(link) ? null : Link.create({ ...link, nodeId: this.siblingIds[link.nodeId] });
      if (isNotNull(descendant)) {
        input.subNodeId = descendant.siblingIds[input.subNodeId + ''];
      }
      return input;
    });
  }

  private normalizeNodeOutputs(nid: string, outputs: { [p: number]: SerializedNodeOutput }, linkResult: LinkResult,
                               descendant?: NodeBranch) {
    return mapToObjectSameKey(outputs, (k, output) => {
      const links = linkResult.getLinkByOutput(this.parent.id, nid, k) ?? [];
      output.links = links.map(link => Link.create({ ...link, nodeId: this.siblingIds[link.nodeId] }));
      if (isNotNull(descendant)) {
        output.subNodeId = descendant.siblingIds[output.subNodeId + ''];
      }
      return output;
    });
  }

}

class ContainerValidator implements IContainerRegistryValidator {
  private readonly container: SerializedContainer;

  constructor(container: SerializedContainer) {
    this.container = container;
  }

  validate(cid: number | string | SerializedContainer | IContainer, strict?: boolean): IContainer {
    return <IContainer>this.container;
  }

}

class NodeValidator implements INodeRegistryValidator {
  private readonly nodes: { [nId: string]: { node: SerializedNode, descendant?: NodeBranch } };

  constructor(nodes: { [p: string]: { node: SerializedNode; descendant?: NodeBranch } }) {
    this.nodes = nodes;
  }

  validate(node: string, container: IContainer, strict?: boolean): INode {
    return <INode>this.nodes[node]?.node;
  }

}
