import { Container, ContainerRegistry } from '../../container';
import { Node } from '../../node';
import { NODE_INFO } from '../../node-info';
import { NodeSetting, SettingType, Type } from '../../node-io';
import { Link, SlimNodeIO } from '../../node-link';

export class LinkErrorCheckNode extends Node {
  lastTrigger;

  constructor() {
    super();
    this.title = NODE_INFO.error['link-check'].title;
    this.description = NODE_INFO.error['link-check'].description;
    this.addInput('trigger', Type.BOOLEAN);
    this.addOutput('errorsCount', Type.NUMBER);
    this.addOutput('errorsJSON', Type.STRING);
    //this.addOutput('unresolvedErrors', Type.NUMBER);
    //this.addOutput('progress', Type.STRING);

    this.settings['allContainer'] = NodeSetting.create({
                                                         description: 'Search All Containers?',
                                                         value: true,
                                                         type: SettingType.BOOLEAN,
                                                       });
    this.settings['subContainers'] = NodeSetting.create({
                                                          description: 'Search Sub-Containers?',
                                                          value: true,
                                                          type: SettingType.BOOLEAN,
                                                        });
    this.setSettingsConfig({
                             groups: [],
                             conditions: {
                               subContainers: setting => {
                                 return !setting['allContainer'].value;
                               },
                             },
                           });

  }

  init() {
  }

  onCreated() {
  }

  onAdded() {
    this.properties['errors'] = {};
    this.properties['errorsCount'] = 0;
    this.lastTrigger = false;
    this.setOutputData(0, 0);
    this.setOutputData(1, '{}');
    //this.setOutputData(2, null);
    //this.onAfterSettingsChange();
  }

  onInputUpdated() {
    let trigger = this.getInputData(0);
    if (trigger && !this.lastTrigger) {
      //console.log('THIS', this)
      //console.log('this.cid', this.cid)
      //console.log('CONTAINERS', Container.containers)
      this.properties['errors'] = {};
      this.properties['errorsCount'] = 0;
      const searchThisContainer = this.settings['allContainer'].value ? ContainerRegistry.instance().rootContainer()
                                                                      : ContainerRegistry.instance().get(this.cid);
      const searchSubContainers = this.settings['allContainer'].value ? true : this.settings['subContainers'].value;
      this.checkLinksForInputOutputData(searchThisContainer, searchSubContainers);
    }
    this.lastTrigger = trigger;
  }

  onAfterSettingsChange() {
  }

  checkLinksForInputOutputData(searchContainer, includeSubContainers = false, isTopRecursion = true) {
    //console.log('SEARCH CONTAINER', searchContainer)
    const currentContainer = searchContainer.id;
    let containers = [];
    let errors = 0;
    const nodeList = searchContainer.getAllNodes();
    const numNodes = nodeList.length;
    //console.log('numNodes', numNodes)
    //let currentNodeNum = 0;
    for (let x = 0; x < numNodes; x++) {
      //currentNodeNum++;
      //this.setOutputData(2, `${currentNodeNum}/${numNodes}`);
      console.log(x, nodeList[x]);
      if (nodeList[x].isContainer) {
        //console.log('FOUND CONTAINER', nodeList[x]);
        containers.push(nodeList[x].id);
        continue;
      }
      //let node: Node = this.container._nodes[`${this.inputs[i].link.target_node_id}`];
      const node: Node = searchContainer.getNodeById(Number(nodeList[x]['id']));
      //console.log('node', x, node);
      if (!node.hasOwnProperty('outputs')) continue;

      let outputs = node.outputs;
      let outputLinks = [];
      for (const output in outputs) {
        if (outputs[output].links != null) {
          console.log('links', output, outputs[output].links);
          outputLinks.push([output, outputs[output].data, outputs[output].links.map(l => Link.create(l))]);
        }
      }
      console.log('outputLinks', outputLinks);

      outputLinks.forEach(linkInfo => {
        console.log('linkInfo', linkInfo);
        linkInfo[2].map(l => Link.create(l)).forEach((link: SlimNodeIO) => {
          try {
            let error = false;
            const receiver: Node = searchContainer.getNodeById(link.nodeId);
            console.log('receiver.inputs', receiver.inputs);
            if (receiver.inputs[link.ioId].data != linkInfo[1]) {
              console.log('LINK ERROR: output data does not match input data.');
              error = true;
            }
            if (receiver.inputs[link.ioId].link.slot != linkInfo[0] &&
                receiver.inputs[link.ioId].link.nodeId != nodeList[x]['id']) {
              console.log('LINK ERROR: output link reference does not match input link reference.');
              error = true;
            }
            if (error) {
              errors++;
              console.log('ERROR');
              if (!this.properties['errors'].hasOwnProperty(
                currentContainer)) {
                this.properties['errors'][currentContainer] = {
                  containerName: searchContainer.name,
                  containerErrors: {},
                };
              }
              if (!this.properties['errors'][currentContainer]['containerErrors'].hasOwnProperty(
                nodeList[x]['id'])) {
                this.properties['errors'][currentContainer]['containerErrors'][nodeList[x]['id']] = {
                  nodeName: nodeList[x].name,
                  nodeErrors: {},
                };
              }
              this.properties['errors'][currentContainer]['containerErrors'][nodeList[x]['id']]['nodeErrors'][linkInfo[0]] = {
                outputName: node.outputs[linkInfo[0]].name,
                outputNum: linkInfo[0],
                inputNode: receiver.id,
                inputNodeName: receiver.name,
                inputNodeSlot: receiver.inputs[link.ioId].link.slot,
              };
              // CALL onInputUpdated() ON RECEIVER NODE.  I DONT KNOW HOW TO DO IT.
            }
          } catch {
            errors++;
            console.log('UNKNOWN ERROR');
            if (!this.properties['errors'].hasOwnProperty(
              currentContainer)) {
              this.properties['errors'][currentContainer] = {
                containerName: searchContainer.name,
                containerErrors: {},
              };
            }
            if (!this.properties['errors'][currentContainer]['containerErrors'].hasOwnProperty(
              nodeList[x]['id'])) {
              this.properties['errors'][currentContainer]['containerErrors'][nodeList[x]['id']] = {
                nodeName: nodeList[x].name,
                nodeErrors: {},
              };
            }
          }
        });
      });
    }

    this.properties['errorsCount'] += errors;
    if (this.properties['errors'].hasOwnProperty(
      currentContainer)) {
      this.properties['errors'][currentContainer]['errorsCount'] = errors;
    }
    //console.log('containersList', containers);
    if (includeSubContainers) {
      containers.forEach(containerNum => {
        //console.log('containerNum', typeof containerNum, containerNum);
        this.checkLinksForInputOutputData(ContainerRegistry.instance().get(containerNum), includeSubContainers, false);
      });
    }
    if (isTopRecursion) {
      this.setOutputData(0, this.properties['errorsCount']);
      this.properties['errors']['totalErrors'] = this.properties['errorsCount'];
      this.setOutputData(1, JSON.stringify(this.properties['errors']));
    }
  }

}

Container.registerNodeType('error/link-check', LinkErrorCheckNode);
